/**
 * ExportButton - Button to export project as ZIP
 */

import React, { useState } from 'react';
import { Button, Modal, Form, Input, Progress, Space, Typography, message } from 'antd';
import { ExportOutlined, FolderOutlined, FileOutlined } from '@ant-design/icons';
import type { Page } from '@core/types/analysis';
import type { Menu } from '@core/types/menu';
import type { Project } from '@core/types/project';
import { ProjectGenerator } from '@core/generators/project-generator';
import { exportAndDownloadProject, calculateProjectSize } from '@core/generators/zip-exporter';
import { useI18n } from '../i18n';

const { Text } = Typography;

interface ExportButtonProps {
  pages: Page[];
  menu?: Menu;
  sessionId: string;
  disabled?: boolean;
}

interface ExportFormValues {
  projectName: string;
}

const ExportButton: React.FC<ExportButtonProps> = ({
  pages,
  menu,
  sessionId,
  disabled = false,
}) => {
  const { t, language } = useI18n();
  const [modalOpen, setModalOpen] = useState(false);
  const [exporting, setExporting] = useState(false);
  const [progress, setProgress] = useState(0);
  const [project, setProject] = useState<Project | null>(null);
  const [form] = Form.useForm<ExportFormValues>();

  const handleExport = async (values: ExportFormValues) => {
    if (pages.length === 0) {
      message.warning(language === 'en' ? 'No pages to export' : '');
      return;
    }

    setExporting(true);
    setProgress(10);

    try {
      // Generate project
      const generator = new ProjectGenerator({
        sessionId,
        name: values.projectName,
        framework: 'react-antd',
        includeReadme: true,
        outputFormat: 'zip',
      });

      setProgress(30);

      const generatedProject = await generator.generate(pages, menu);
      setProject(generatedProject);

      setProgress(70);

      // Export as ZIP
      await exportAndDownloadProject(generatedProject);

      setProgress(100);

      message.success(t.exportSuccess);

      // Close modal after a short delay
      setTimeout(() => {
        setModalOpen(false);
        setExporting(false);
        setProgress(0);
      }, 1000);
    } catch (error) {
      console.error('Export failed:', error);
      message.error(t.exportFailed + '' + (error instanceof Error ? error.message : 'Unknown error'));
      setExporting(false);
      setProgress(0);
    }
  };

  const handleCancel = () => {
    if (!exporting) {
      setModalOpen(false);
      form.resetFields();
      setProject(null);
    }
  };

  const projectSize = project ? calculateProjectSize(project) : null;

  return (
    <>
      <Button
        type="primary"
        icon={<ExportOutlined />}
        onClick={() => setModalOpen(true)}
        disabled={disabled || pages.length === 0}
        block
      >
        {t.btnExportProject}
      </Button>

      <Modal
        title={t.exportTitle}
        open={modalOpen}
        onCancel={handleCancel}
        footer={null}
        width={480}
        maskClosable={!exporting}
        closable={!exporting}
      >
        <Form
          form={form}
          layout="vertical"
          onFinish={handleExport}
          initialValues={{
            projectName: 'my-admin',
          }}
        >
          <Form.Item
            name="projectName"
            label={t.exportProjectName}
            rules={[
              { required: true, message: t.exportProjectNamePlaceholder },
              { pattern: /^[a-z][a-z0-9-]*$/, message: language === 'en' ? 'Only lowercase letters, numbers, and hyphens allowed, must start with a letter' : '' },
            ]}
          >
            <Input 
              placeholder="my-admin" 
              disabled={exporting}
              prefix={<FolderOutlined />}
            />
          </Form.Item>

          {/* Export Info */}
          <div style={{ marginBottom: 16 }}>
            <Space direction="vertical" style={{ width: '100%' }}>
              <div>
                <Text type="secondary">{language === 'en' ? 'Pages: ' : ''}</Text>
                <Text strong>{pages.length}</Text>
              </div>
              {menu && (
                <div>
                  <Text type="secondary">{t.menuItems}</Text>
                  <Text strong>{menu.items.length} {t.topLevelMenus}</Text>
                </div>
              )}
              <div>
                <Text type="secondary">{language === 'en' ? 'Framework: ' : ''}</Text>
                <Text strong>React + Ant Design + Vite</Text>
              </div>
            </Space>
          </div>

          {/* Progress */}
          {exporting && (
            <div style={{ marginBottom: 16 }}>
              <Progress 
                percent={progress} 
                status={progress === 100 ? 'success' : 'active'}
                strokeColor={{
                  '0%': '#108ee9',
                  '100%': '#87d068',
                }}
              />
              <Text type="secondary">
                {progress < 30 && (language === 'en' ? 'Generating project structure...' : '...')}
                {progress >= 30 && progress < 70 && (language === 'en' ? 'Generating page components...' : '...')}
                {progress >= 70 && progress < 100 && (language === 'en' ? 'Packing ZIP...' : ' ZIP...')}
                {progress === 100 && (language === 'en' ? 'Export complete!' : '')}
              </Text>
            </div>
          )}

          {/* Project Info */}
          {projectSize && !exporting && (
            <div style={{ 
              marginBottom: 16, 
              padding: 12, 
              background: '#f5f5f5', 
              borderRadius: 6 
            }}>
              <Space direction="vertical" size="small">
                <div>
                  <FileOutlined style={{ marginRight: 8 }} />
                  <Text>{language === 'en' ? 'Files: ' : ''}{projectSize.fileCount}</Text>
                </div>
                <div>
                  <Text type="secondary" style={{ fontSize: 12 }}>
                    {language === 'en' ? 'Total size: ' : ''}{(projectSize.totalSize / 1024).toFixed(2)} KB
                  </Text>
                </div>
              </Space>
            </div>
          )}

          <Form.Item style={{ marginBottom: 0 }}>
            <Space style={{ width: '100%', justifyContent: 'flex-end' }}>
              <Button onClick={handleCancel} disabled={exporting}>
                {t.exportCancel}
              </Button>
              <Button 
                type="primary" 
                htmlType="submit" 
                loading={exporting}
                icon={<ExportOutlined />}
              >
                {exporting ? t.exporting : t.exportConfirm + ' ZIP'}
              </Button>
            </Space>
          </Form.Item>
        </Form>
      </Modal>
    </>
  );
};

export default ExportButton;
