/**
 * i18n Context - Provide translations throughout the app
 */

import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import { locales, Language, Translations } from './locales';

interface I18nContextType {
  language: Language;
  setLanguage: (lang: Language) => void;
  t: Translations;
  formatMessage: (key: keyof Translations, params?: Record<string, string | number>) => string;
}

const I18nContext = createContext<I18nContextType | null>(null);

const STORAGE_KEY = 'admin-clone-language';

export const I18nProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [language, setLanguageState] = useState<Language>('zh-CN');
  const [isLoaded, setIsLoaded] = useState(false);

  // Load saved language on mount
  useEffect(() => {
    chrome.storage.local.get([STORAGE_KEY], (result) => {
      if (result[STORAGE_KEY] && locales[result[STORAGE_KEY] as Language]) {
        setLanguageState(result[STORAGE_KEY] as Language);
      }
      setIsLoaded(true);
    });
  }, []);

  // Save language when changed
  const setLanguage = useCallback((lang: Language) => {
    setLanguageState(lang);
    chrome.storage.local.set({ [STORAGE_KEY]: lang });
  }, []);

  // Get translations for current language
  const t = locales[language];

  // Format message with parameters
  const formatMessage = useCallback((key: keyof Translations, params?: Record<string, string | number>) => {
    let message = t[key];
    if (params) {
      Object.entries(params).forEach(([paramKey, value]) => {
        message = message.replace(`{${paramKey}}`, String(value));
      });
    }
    return message;
  }, [t]);

  if (!isLoaded) {
    return null; // Or a loading spinner
  }

  return (
    <I18nContext.Provider value={{ language, setLanguage, t, formatMessage }}>
      {children}
    </I18nContext.Provider>
  );
};

export const useI18n = (): I18nContextType => {
  const context = useContext(I18nContext);
  if (!context) {
    throw new Error('useI18n must be used within an I18nProvider');
  }
  return context;
};
