import { useState, useMemo, useEffect } from 'react';
import { Layout, Typography, Tabs, Space, Spin, Button, Tooltip } from 'antd';
import { 
  ScanOutlined, 
  CodeOutlined, 
  FileTextOutlined,
  SettingOutlined,
  PushpinOutlined,
} from '@ant-design/icons';
import { useAnalysis } from './hooks/useAnalysis';
import { useMenu } from './hooks/useMenu';
import { useTraversal } from './hooks/useTraversal';
import { AnalysisPanel, CodePreview, PagesPanel, SettingsPanel, ErrorBoundary } from './components';
import { generateId } from '@core/utils/dom-utils';
import { saveActiveTab, loadActiveTab } from './utils/state-storage';
import { generateFullPageCode } from '@core/generators/component-generator';
import { I18nProvider, useI18n } from './i18n';
import type { GeneratedCode } from '@core/types/project';

const { Header, Content } = Layout;
const { Title, Text } = Typography;

function AppContent() {
  const { t } = useI18n();
  const analysis = useAnalysis();
  const menuState = useMenu();
  const traversalState = useTraversal();
  const [activeTab, setActiveTab] = useState('analysis');
  const [tabLoaded, setTabLoaded] = useState(false);
  const [sidePanelAvailable, setSidePanelAvailable] = useState(false);
  const surface = typeof window !== 'undefined' ? detectSurface() : 'popup';
  const isSidePanel = surface === 'sidepanel';
  
  // Generate a session ID for this instance
  const sessionId = useMemo(() => generateId(), []);
  
  const { page, generatedCodes, isLoading: analysisLoading } = analysis;
  const { isLoading: menuLoading } = menuState;
  const { isLoading: traversalLoading, analyzedPages } = traversalState;
  
  const isLoading = analysisLoading || menuLoading || traversalLoading || !tabLoaded;

  // Generate codes for all analyzed pages (from traversal)
  // useMemo to avoid regenerating on every render
  const allGeneratedCodes = useMemo<GeneratedCode[]>(() => {
    // If we have traversed pages, generate codes for all of them
    if (analyzedPages && analyzedPages.length > 0) {
      const codes: GeneratedCode[] = [];
      for (const p of analyzedPages) {
        const pageCodes = generateFullPageCode(p);
        codes.push(...pageCodes);
      }
      return codes;
    }
    // Otherwise use single page analysis codes
    return generatedCodes;
  }, [analyzedPages, generatedCodes]);

  // Calculate pages count for tab label
  const pagesCount = analyzedPages.length > 0 ? analyzedPages.length : (page ? 1 : 0);
  const hasPages = menuState.status === 'done' || analyzedPages.length > 0;

  // Load active tab on mount
  useEffect(() => {
    loadActiveTab().then(savedTab => {
      if (savedTab) {
        setActiveTab(savedTab);
      }
      setTabLoaded(true);
    });
  }, []);

  useEffect(() => {
    const hasSidePanelApi =
      typeof chrome !== 'undefined' &&
      typeof chrome?.sidePanel?.open === 'function' &&
      typeof chrome?.tabs?.query === 'function';
    if (hasSidePanelApi) {
      setSidePanelAvailable(true);
    }
  }, []);

  // Save active tab when changed
  const handleTabChange = (key: string) => {
    setActiveTab(key);
    saveActiveTab(key);
  };

  // Handle view code button click
  const handleViewCode = () => {
    setActiveTab('code');
    saveActiveTab('code');
  };

  const handleOpenSidePanel = async () => {
    if (typeof chrome === 'undefined' || !chrome?.sidePanel?.open || !chrome?.tabs?.query) {
      return;
    }
    try {
      const [tab] = await chrome.tabs.query({ active: true, currentWindow: true });
      if (tab?.windowId !== undefined) {
        await chrome.sidePanel.open({ windowId: tab.windowId });
      }
    } catch (error) {
      console.error('[App] Failed to open side panel', error);
    }
  };

  const tabItems = [
    {
      key: 'analysis',
      label: (
        <Space>
          <ScanOutlined />
          {t.tabAnalysis}
        </Space>
      ),
      children: (
        <AnalysisPanel 
          analysis={analysis} 
          menuState={menuState} 
          traversalState={traversalState} 
          sessionId={sessionId}
          onViewCode={handleViewCode}
        />
      ),
    },
    {
      key: 'pages',
      label: (
        <Space>
          <FileTextOutlined />
          {t.tabPages} {pagesCount > 0 && `(${pagesCount})`}
        </Space>
      ),
      children: (
        <PagesPanel 
          traversalState={traversalState}
          menuState={menuState}
        />
      ),
      disabled: !hasPages,
    },
    {
      key: 'code',
      label: (
        <Space>
          <CodeOutlined />
          {t.tabCode} {allGeneratedCodes.length > 0 && `(${allGeneratedCodes.length})`}
        </Space>
      ),
      children: (
        <CodePreview codes={allGeneratedCodes} />
      ),
      disabled: allGeneratedCodes.length === 0,
    },
    {
      key: 'settings',
      label: (
        <Space>
          <SettingOutlined />
          {t.tabSettings}
        </Space>
      ),
      children: (
        <SettingsPanel />
      ),
    },
  ];

  const layoutStyle = isSidePanel
    ? { width: '100%', minHeight: '100vh', height: '100vh' }
    : { width: 420, minHeight: 500, maxHeight: 600 };

  const contentStyle = isSidePanel
    ? { overflow: 'auto', height: 'calc(100vh - 48px)' }
    : { overflow: 'auto' };

  return (
    <Layout style={layoutStyle}>
      <Header 
        style={{ 
          background: '#fff', 
          padding: '0 16px', 
          height: 48,
          lineHeight: '48px',
          borderBottom: '1px solid #f0f0f0',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
        }}
      >
        <Title level={5} style={{ margin: 0 }}>
           {t.appName}
        </Title>
        <Space size="small">
          <Text type="secondary" style={{ fontSize: 12 }}>
            v0.1.0
          </Text>
          {sidePanelAvailable && !isSidePanel && (
            <Tooltip title={t.openSidePanelHint}>
              <Button size="small" icon={<PushpinOutlined />} onClick={handleOpenSidePanel}>
                {t.openSidePanel}
              </Button>
            </Tooltip>
          )}
        </Space>
      </Header>
      
      <Content style={contentStyle}>
        {isLoading ? (
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: 200 }}>
            <Spin tip={t.loading} />
          </div>
        ) : (
          <Tabs
            activeKey={activeTab}
            onChange={handleTabChange}
            items={tabItems}
            size="small"
            style={{ padding: '0 8px' }}
            tabBarStyle={{ marginBottom: 8 }}
          />
        )}
      </Content>
    </Layout>
  );
}

function App() {
  return (
    <ErrorBoundary>
      <I18nProvider>
        <AppContent />
      </I18nProvider>
    </ErrorBoundary>
  );
}

function detectSurface(): 'popup' | 'sidepanel' {
  if (typeof window === 'undefined') {
    return 'popup';
  }
  const dataSurface = document?.body?.dataset?.surface;
  if (dataSurface === 'sidepanel') {
    return 'sidepanel';
  }
  if (window.location.pathname.includes('sidepanel')) {
    return 'sidepanel';
  }
  return 'popup';
}

export default App;
