/**
 * ProgressBar Component - Display traversal progress
 */

import React from 'react';
import { Progress, Typography, Space, Tag } from 'antd';
import { 
  ClockCircleOutlined,
  CheckCircleOutlined,
  CloseCircleOutlined,
} from '@ant-design/icons';
import type { TraversalProgress } from '@content-scripts/traverser/page-traverser';

const { Text } = Typography;

interface ProgressBarProps {
  progress: TraversalProgress | null;
  showDetails?: boolean;
}

/**
 * Format milliseconds to readable time
 */
function formatTime(ms: number): string {
  if (ms < 1000) return '< 1';
  
  const seconds = Math.floor(ms / 1000);
  const minutes = Math.floor(seconds / 60);
  const hours = Math.floor(minutes / 60);
  
  if (hours > 0) {
    return `${hours}${minutes % 60}`;
  }
  if (minutes > 0) {
    return `${minutes}${seconds % 60}`;
  }
  return `${seconds}`;
}

/**
 * Get status color
 */
function getStatusColor(status: TraversalProgress['status']): string {
  switch (status) {
    case 'running': return 'processing';
    case 'paused': return 'warning';
    case 'completed': return 'success';
    case 'error': return 'error';
    case 'cancelled': return 'default';
    default: return 'default';
  }
}

/**
 * Get status text
 */
function getStatusText(status: TraversalProgress['status']): string {
  switch (status) {
    case 'idle': return '';
    case 'running': return '';
    case 'paused': return '';
    case 'completed': return '';
    case 'error': return '';
    case 'cancelled': return '';
    default: return '';
  }
}

const ProgressBar: React.FC<ProgressBarProps> = ({ progress, showDetails = true }) => {
  if (!progress) {
    return (
      <div style={{ textAlign: 'center', padding: 16 }}>
        <Text type="secondary"></Text>
      </div>
    );
  }
  
  const { status, totalItems, completedItems, failedItems, elapsedTime, estimatedRemainingTime, currentItem } = progress;
  const percent = totalItems > 0 ? Math.round((completedItems / totalItems) * 100) : 0;
  const isActive = status === 'running';
  
  return (
    <div className="progress-bar">
      {/* Status Tag */}
      <div style={{ marginBottom: 8, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Tag color={getStatusColor(status)}>{getStatusText(status)}</Tag>
        <Text type="secondary" style={{ fontSize: 12 }}>
          {completedItems} / {totalItems}
        </Text>
      </div>
      
      {/* Progress Bar */}
      <Progress
        percent={percent}
        status={isActive ? 'active' : status === 'error' ? 'exception' : status === 'completed' ? 'success' : 'normal'}
        strokeColor={status === 'paused' ? '#faad14' : undefined}
        size="small"
      />
      
      {/* Current Item */}
      {isActive && currentItem && (
        <div style={{ marginTop: 8 }}>
          <Text type="secondary" style={{ fontSize: 12 }}>
            : {currentItem.title}
          </Text>
        </div>
      )}
      
      {/* Details */}
      {showDetails && (
        <div style={{ marginTop: 12 }}>
          <Space split={<span style={{ color: '#d9d9d9' }}>|</span>} size={8}>
            {/* Elapsed Time */}
            <Space size={4}>
              <ClockCircleOutlined style={{ fontSize: 12, color: '#1890ff' }} />
              <Text type="secondary" style={{ fontSize: 12 }}>
                : {formatTime(elapsedTime)}
              </Text>
            </Space>
            
            {/* Remaining Time */}
            {isActive && estimatedRemainingTime > 0 && (
              <Text type="secondary" style={{ fontSize: 12 }}>
                : ~{formatTime(estimatedRemainingTime)}
              </Text>
            )}
          </Space>
          
          {/* Failed Items */}
          {failedItems.length > 0 && (
            <div style={{ marginTop: 8 }}>
              <Space size={4}>
                <CloseCircleOutlined style={{ fontSize: 12, color: '#ff4d4f' }} />
                <Text type="danger" style={{ fontSize: 12 }}>
                  : {failedItems.length} 
                </Text>
              </Space>
            </div>
          )}
          
          {/* Completion */}
          {status === 'completed' && (
            <div style={{ marginTop: 8 }}>
              <Space size={4}>
                <CheckCircleOutlined style={{ fontSize: 12, color: '#52c41a' }} />
                <Text type="success" style={{ fontSize: 12 }}>
                   {completedItems - failedItems.length} 
                </Text>
              </Space>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default ProgressBar;
