/**
 * State Storage - Persist popup state across open/close cycles
 * Uses chrome.storage.session for temporary session data
 */

import type { Page } from '@core/types/analysis';
import type { Menu } from '@core/types/menu';
import type { GeneratedCode } from '@core/types/project';
import type { TraversalProgress, TraversalStatus } from '@content-scripts/traverser/page-traverser';

// Storage keys
const STORAGE_KEYS = {
  ANALYSIS_STATE: 'popup_analysis_state',
  MENU_STATE: 'popup_menu_state',
  TRAVERSAL_STATE: 'popup_traversal_state',
  ACTIVE_TAB: 'popup_active_tab',
} as const;

// Analysis state type for storage
export interface StoredAnalysisState {
  status: 'idle' | 'analyzing' | 'generating' | 'done' | 'error';
  page: Page | null;
  generatedCodes: GeneratedCode[];
  error: string | null;
  progress: number;
  timestamp: number;
}

// Menu state type for storage
export interface StoredMenuState {
  status: 'idle' | 'analyzing' | 'done' | 'error';
  menu: Menu | null;
  error: string | null;
  timestamp: number;
}

// Traversal state type for storage
export interface StoredTraversalState {
  status: TraversalStatus;
  traversalId: string | null;
  progress: TraversalProgress | null;
  analyzedPages: Page[];
  error: string | null;
  timestamp: number;
}

/**
 * Save analysis state
 */
export async function saveAnalysisState(state: Omit<StoredAnalysisState, 'timestamp'>): Promise<void> {
  try {
    const data: StoredAnalysisState = {
      ...state,
      timestamp: Date.now(),
    };
    await chrome.storage.session.set({ [STORAGE_KEYS.ANALYSIS_STATE]: data });
  } catch (error) {
    console.error('[StateStorage] Failed to save analysis state:', error);
  }
}

/**
 * Load analysis state
 */
export async function loadAnalysisState(): Promise<StoredAnalysisState | null> {
  try {
    const result = await chrome.storage.session.get(STORAGE_KEYS.ANALYSIS_STATE);
    const state = result[STORAGE_KEYS.ANALYSIS_STATE] as StoredAnalysisState | undefined;
    
    // Check if state is stale (older than 1 hour)
    if (state && Date.now() - state.timestamp > 60 * 60 * 1000) {
      await clearAnalysisState();
      return null;
    }
    
    return state || null;
  } catch (error) {
    console.error('[StateStorage] Failed to load analysis state:', error);
    return null;
  }
}

/**
 * Clear analysis state
 */
export async function clearAnalysisState(): Promise<void> {
  try {
    await chrome.storage.session.remove(STORAGE_KEYS.ANALYSIS_STATE);
  } catch (error) {
    console.error('[StateStorage] Failed to clear analysis state:', error);
  }
}

