/**
 * Save active tab index
 */
export async function saveActiveTab(tabKey: string): Promise<void> {
  try {
    await chrome.storage.session.set({ [STORAGE_KEYS.ACTIVE_TAB]: tabKey });
  } catch (error) {
    console.error('[StateStorage] Failed to save active tab:', error);
  }
}

/**
 * Load active tab index
 */
export async function loadActiveTab(): Promise<string | null> {
  try {
    const result = await chrome.storage.session.get(STORAGE_KEYS.ACTIVE_TAB);
    return result[STORAGE_KEYS.ACTIVE_TAB] as string || null;
  } catch (error) {
    console.error('[StateStorage] Failed to load active tab:', error);
    return null;
  }
}

/**
 * Clear all popup state
 */
export async function clearAllState(): Promise<void> {
  try {
    await chrome.storage.session.remove(Object.values(STORAGE_KEYS));
  } catch (error) {
    console.error('[StateStorage] Failed to clear all state:', error);
  }
}
