/**
 * SessionDetail - Display detailed information about a session
 */

import React from 'react';
import { Card, Descriptions, Typography, Space, Tag, Button, Divider, List } from 'antd';
import {
  ClockCircleOutlined,
  FileOutlined,
  CheckCircleOutlined,
  ExclamationCircleOutlined,
  ExportOutlined,
  ReloadOutlined,
} from '@ant-design/icons';
import type { AnalysisSession } from '@core/types/session';
import type { Page } from '@core/types/analysis';

const { Text, Title } = Typography;

interface SessionDetailProps {
  session: AnalysisSession;
  pages?: Page[];
  onExport?: () => void;
  onReanalyze?: () => void;
  onClose?: () => void;
}

/**
 * Format timestamp to full date time
 */
function formatDateTime(timestamp: number): string {
  return new Date(timestamp).toLocaleString('zh-CN', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit',
  });
}

/**
 * Format duration in milliseconds to readable string
 */
function formatDuration(ms: number): string {
  const seconds = Math.floor(ms / 1000);
  const minutes = Math.floor(seconds / 60);
  const hours = Math.floor(minutes / 60);
  
  if (hours > 0) {
    return `${hours}  ${minutes % 60} `;
  }
  if (minutes > 0) {
    return `${minutes}  ${seconds % 60} `;
  }
  return `${seconds} `;
}

/**
 * Get status info
 */
function getStatusInfo(status: AnalysisSession['status']): { color: string; text: string; icon: React.ReactNode } {
  switch (status) {
    case 'analyzing':
    case 'traversing':
      return { color: 'processing', text: '', icon: <ClockCircleOutlined /> };
    case 'completed':
      return { color: 'success', text: '', icon: <CheckCircleOutlined /> };
    case 'paused':
      return { color: 'warning', text: '', icon: <ClockCircleOutlined /> };
    case 'failed':
      return { color: 'error', text: '', icon: <ExclamationCircleOutlined /> };
    case 'exporting':
      return { color: 'processing', text: '', icon: <ClockCircleOutlined /> };
    default:
      return { color: 'default', text: '', icon: null };
  }
}

const SessionDetail: React.FC<SessionDetailProps> = ({
  session,
  pages = [],
  onExport,
  onReanalyze,
  onClose,
}) => {
  const statusInfo = getStatusInfo(session.status);
  const duration = session.completedAt 
    ? session.completedAt - session.createdAt
    : Date.now() - session.createdAt;

  return (
    <Card 
      size="small" 
      bordered={false}
      title={
        <Space>
          <Title level={5} style={{ margin: 0 }}>{session.name}</Title>
          <Tag color={statusInfo.color} icon={statusInfo.icon}>
            {statusInfo.text}
          </Tag>
        </Space>
      }
      extra={onClose && (
        <Button type="text" size="small" onClick={onClose}>
          
        </Button>
      )}
    >
      <Space direction="vertical" style={{ width: '100%' }} size="middle">
        {/* Basic Info */}
        <Descriptions size="small" column={1}>
          <Descriptions.Item label="">
            <Text copyable style={{ fontSize: 12 }}>
              {session.rootUrl || ''}
            </Text>
          </Descriptions.Item>
          <Descriptions.Item label="">
            {formatDateTime(session.createdAt)}
          </Descriptions.Item>
          {session.completedAt && (
            <Descriptions.Item label="">
              {formatDateTime(session.completedAt)}
            </Descriptions.Item>
          )}
          <Descriptions.Item label="">
            {formatDuration(duration)}
          </Descriptions.Item>
        </Descriptions>

        <Divider style={{ margin: '8px 0' }} />

        {/* Statistics */}
        <Space size="large">
          <div style={{ textAlign: 'center' }}>
            <div>
              <Text strong style={{ fontSize: 24, color: '#1890ff' }}>
                {session.analyzedPages}
              </Text>
            </div>
            <Text type="secondary" style={{ fontSize: 12 }}></Text>
          </div>
          <div style={{ textAlign: 'center' }}>
            <div>
              <Text strong style={{ fontSize: 24, color: '#52c41a' }}>
                {session.totalPages}
              </Text>
            </div>
            <Text type="secondary" style={{ fontSize: 12 }}></Text>
          </div>
          <div style={{ textAlign: 'center' }}>
            <div>
              <Text strong style={{ fontSize: 24, color: session.failedPages > 0 ? '#ff4d4f' : '#722ed1' }}>
                {session.failedPages}
              </Text>
            </div>
            <Text type="secondary" style={{ fontSize: 12 }}></Text>
          </div>
        </Space>

        <Divider style={{ margin: '8px 0' }} />

        {/* Analyzed Pages */}
        {pages.length > 0 && (
          <>
            <Text strong></Text>
            <List
              size="small"
              dataSource={pages.slice(0, 5)}
              renderItem={(page) => (
                <List.Item style={{ padding: '4px 0' }}>
                  <Space>
                    <FileOutlined />
                    <Text ellipsis style={{ maxWidth: 200 }}>
                      {page.title || page.path || ''}
                    </Text>
                    <Text type="secondary" style={{ fontSize: 12 }}>
                      {page.components.length} 
                    </Text>
                  </Space>
                </List.Item>
              )}
              footer={pages.length > 5 && (
                <Text type="secondary" style={{ fontSize: 12 }}>
                  ... {pages.length - 5} 
                </Text>
              )}
            />
          </>
        )}

        {/* Actions */}
        <Space style={{ width: '100%' }}>
          {onExport && session.status === 'completed' && (
            <Button
              type="primary"
              icon={<ExportOutlined />}
              onClick={onExport}
              style={{ flex: 1 }}
            >
              
            </Button>
          )}
          {onReanalyze && (
            <Button
              icon={<ReloadOutlined />}
              onClick={onReanalyze}
              style={{ flex: 1 }}
            >
              
            </Button>
          )}
        </Space>

        {/* Error Message */}
        {session.status === 'failed' && session.traversalState?.failedMenuItems && session.traversalState.failedMenuItems.length > 0 && (
          <Card size="small" style={{ background: '#fff2f0' }}>
            <Space>
              <ExclamationCircleOutlined style={{ color: '#ff4d4f' }} />
              <Text type="danger">
                {session.traversalState.failedMenuItems.length} 
              </Text>
            </Space>
          </Card>
        )}
      </Space>
    </Card>
  );
};

export default SessionDetail;
