/**
 * useTraversal Hook - Manage page traversal state
 */

import { useState, useCallback, useRef, useEffect } from 'react';
import type { Menu } from '@core/types/menu';
import type { Page } from '@core/types/analysis';
import type { TraversalProgress, TraversalStatus } from '@content-scripts/traverser/page-traverser';
import { saveTraversalState, loadTraversalState, clearTraversalState } from '../utils/state-storage';

export interface TraversalState {
  status: TraversalStatus;
  progress: TraversalProgress | null;
  analyzedPages: Page[];
  error: string | null;
}

export interface UseTraversalReturn extends TraversalState {
  startTraversal: (menu: Menu) => Promise<void>;
  pauseTraversal: () => void;
  resumeTraversal: () => void;
  stopTraversal: () => void;
  reset: () => void;
  isLoading: boolean;
}

const initialState: TraversalState = {
  status: 'idle',
  progress: null,
  analyzedPages: [],
  error: null,
};

export function useTraversal(): UseTraversalReturn {
  const [state, setState] = useState<TraversalState>(initialState);
  const [isLoading, setIsLoading] = useState(true);
  const traversalIdRef = useRef<string | null>(null);
  const messageListenerRef = useRef<((message: any) => void) | null>(null);

  // Load persisted state on mount and check if traversal is still running
  useEffect(() => {
    loadTraversalState().then(async savedState => {
      if (savedState && savedState.traversalId) {
        // Check if traversal is still active in background
        try {
          const response = await chrome.runtime.sendMessage({
            type: 'GET_TRAVERSAL_STATUS',
            traversalId: savedState.traversalId,
          });
          
          if (response.success) {
            // Traversal is still running, restore state
            traversalIdRef.current = savedState.traversalId;
            setState({
              status: response.status,
              progress: savedState.progress,
              analyzedPages: savedState.analyzedPages,
              error: savedState.error,
            });
          } else {
            // Traversal no longer exists, but keep the results
            if (savedState.analyzedPages.length > 0) {
              setState({
                status: savedState.status === 'running' || savedState.status === 'paused' 
                  ? 'completed' 
                  : savedState.status,
                progress: savedState.progress,
                analyzedPages: savedState.analyzedPages,
                error: null,
              });
            }
          }
        } catch {
          // Background not responding, clear state
          clearTraversalState();
        }
      }
      setIsLoading(false);
    });
  }, []);

  // Persist state changes
  useEffect(() => {
    if (!isLoading) {
      saveTraversalState({
        status: state.status,
        traversalId: traversalIdRef.current,
        progress: state.progress,
        analyzedPages: state.analyzedPages,
        error: state.error,
      });
    }
  }, [state, isLoading]);

  // Setup message listener for traversal updates
  useEffect(() => {
    const handleMessage = (message: any) => {
      if (message.traversalId !== traversalIdRef.current) return;
      
      switch (message.type) {
        case 'TRAVERSAL_PROGRESS':
          setState(prev => ({
            ...prev,
            status: message.progress.status,
            progress: message.progress,
          }));
          break;
          
        case 'TRAVERSAL_PAGE_ANALYZED':
          setState(prev => ({
            ...prev,
            analyzedPages: [...prev.analyzedPages, message.page],
          }));
          break;
          
        case 'TRAVERSAL_ERROR':
          setState(prev => ({
            ...prev,
            error: message.error,
          }));
          break;
          
        case 'TRAVERSAL_COMPLETED':
          setState(prev => ({
            ...prev,
            status: 'completed',
            analyzedPages: message.pages || prev.analyzedPages,
          }));
          break;
      }
    };
    
    messageListenerRef.current = handleMessage;
    chrome.runtime.onMessage.addListener(handleMessage);
    
    return () => {
      if (messageListenerRef.current) {
        chrome.runtime.onMessage.removeListener(messageListenerRef.current);
      }
    };
  }, []);

  const startTraversal = useCallback(async (menu: Menu) => {
    // Generate unique traversal ID
    const traversalId = `traversal-${Date.now()}-${Math.random().toString(36).substring(2, 7)}`;
    traversalIdRef.current = traversalId;
    
    setState(prev => ({
      ...prev,
      status: 'running',
      progress: {
        status: 'running',
        totalItems: menu.items.length,
        completedItems: 0,
        currentItem: null,
        failedItems: [],
        startTime: Date.now(),
        elapsedTime: 0,
        estimatedRemainingTime: 0,
      },
      analyzedPages: [],
      error: null,
    }));

    try {
      // Get current tab
      const [tab] = await chrome.tabs.query({ active: true, currentWindow: true });
      
      if (!tab?.id) {
        throw new Error('');
      }

      // Send start traversal message to background
      chrome.runtime.sendMessage({
        type: 'START_TRAVERSAL',
        traversalId,
        tabId: tab.id,
        menu,
      });
    } catch (error) {
      console.error('[useTraversal] Start failed:', error);
      setState(prev => ({
        ...prev,
        status: 'error',
        error: error instanceof Error ? error.message : '',
      }));
    }
  }, []);

  const pauseTraversal = useCallback(() => {
    if (!traversalIdRef.current) return;
    
    chrome.runtime.sendMessage({
      type: 'PAUSE_TRAVERSAL',
      traversalId: traversalIdRef.current,
    });
    
    setState(prev => ({
      ...prev,
      status: 'paused',
      progress: prev.progress ? { ...prev.progress, status: 'paused' } : null,
    }));
  }, []);

  const resumeTraversal = useCallback(() => {
    if (!traversalIdRef.current) return;
    
    chrome.runtime.sendMessage({
      type: 'RESUME_TRAVERSAL',
      traversalId: traversalIdRef.current,
    });
    
    setState(prev => ({
      ...prev,
      status: 'running',
      progress: prev.progress ? { ...prev.progress, status: 'running' } : null,
    }));
  }, []);

  const stopTraversal = useCallback(() => {
    if (!traversalIdRef.current) return;
    
    chrome.runtime.sendMessage({
      type: 'STOP_TRAVERSAL',
      traversalId: traversalIdRef.current,
    });
    
    setState(prev => ({
      ...prev,
      status: 'cancelled',
      progress: prev.progress ? { ...prev.progress, status: 'cancelled' } : null,
    }));
    
    traversalIdRef.current = null;
  }, []);

  const reset = useCallback(() => {
    traversalIdRef.current = null;
    setState(initialState);
    clearTraversalState();
  }, []);

  return {
    ...state,
    startTraversal,
    pauseTraversal,
    resumeTraversal,
    stopTraversal,
    reset,
    isLoading,
  };
}
