/**
 * CodePreview - Display generated code with syntax highlighting
 */

import React, { useState } from 'react';
import { 
  Card, 
  Tabs, 
  Button, 
  Typography, 
  Empty, 
  message,
  Space,
  Tooltip 
} from 'antd';
import { 
  CopyOutlined, 
  DownloadOutlined,
  FileTextOutlined,
  CodeOutlined 
} from '@ant-design/icons';
import type { GeneratedCode } from '@core/types/project';
import { useI18n } from '../i18n';

const { Text } = Typography;

interface CodePreviewProps {
  codes: GeneratedCode[];
  selectedFile?: string;
  onSelectFile?: (filename: string) => void;
}

/**
 * Simple syntax highlighter for JSX/TSX
 * In a real implementation, you'd use a library like Prism.js or highlight.js
 */
function highlightCode(code: string): React.ReactNode {
  // This is a simplified version - in production use a proper syntax highlighter
  return (
    <pre
      style={{
        backgroundColor: '#f5f5f5',
        padding: 12,
        borderRadius: 4,
        overflow: 'auto',
        maxHeight: 400,
        fontSize: 12,
        lineHeight: 1.5,
        fontFamily: 'Monaco, Consolas, "Courier New", monospace',
        margin: 0,
      }}
    >
      <code>{code}</code>
    </pre>
  );
}

const CodePreview: React.FC<CodePreviewProps> = ({
  codes,
  selectedFile,
  onSelectFile,
}) => {
  const { t } = useI18n();
  const [activeKey, setActiveKey] = useState<string>(selectedFile || codes[0]?.filename || '');

  if (codes.length === 0) {
    return (
      <Empty
        image={Empty.PRESENTED_IMAGE_SIMPLE}
        description={t.noGeneratedCode}
      />
    );
  }

  const handleCopy = async (content: string) => {
    try {
      await navigator.clipboard.writeText(content);
      message.success(t.codeCopied);
    } catch (err) {
      message.error(t.copyFailed);
    }
  };

  const handleDownload = (code: GeneratedCode) => {
    const blob = new Blob([code.content], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = code.filename;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
    message.success(`${t.downloaded} ${code.filename}`);
  };

  const tabItems = codes.map(code => ({
    key: code.filename,
    label: (
      <Space size="small">
        <FileTextOutlined />
        <span>{code.filename}</span>
      </Space>
    ),
    children: (
      <div>
        {/* File info */}
        <div style={{ marginBottom: 8, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Text type="secondary" style={{ fontSize: 12 }}>
            {code.path}
          </Text>
          <Space size="small">
            <Tooltip title={t.copyCode}>
              <Button 
                size="small" 
                icon={<CopyOutlined />}
                onClick={() => handleCopy(code.content)}
              />
            </Tooltip>
            <Tooltip title={t.downloadFile}>
              <Button 
                size="small" 
                icon={<DownloadOutlined />}
                onClick={() => handleDownload(code)}
              />
            </Tooltip>
          </Space>
        </div>
        
        {/* Code display */}
        {highlightCode(code.content)}
      </div>
    ),
  }));

  return (
    <Card 
      size="small"
      title={
        <Space>
          <CodeOutlined />
          <span>{t.generatedCode}</span>
          <Text type="secondary">({codes.length} {t.files})</Text>
        </Space>
      }
      bordered={false}
      bodyStyle={{ padding: 0 }}
    >
      <Tabs
        activeKey={activeKey}
        onChange={(key) => {
          setActiveKey(key);
          onSelectFile?.(key);
        }}
        items={tabItems}
        size="small"
        style={{ padding: '0 12px' }}
      />
    </Card>
  );
};

export default CodePreview;
