/**
 * useMenu Hook - Manage menu analysis state
 */

import { useState, useCallback, useEffect } from 'react';
import type { Menu, MenuItem } from '@core/types/menu';
import { saveMenuState, loadMenuState, clearMenuState } from '../utils/state-storage';

export interface MenuState {
  status: 'idle' | 'analyzing' | 'done' | 'error';
  menu: Menu | null;
  selectedItem: MenuItem | null;
  error: string | null;
}

export interface UseMenuReturn extends MenuState {
  analyzeMenu: () => Promise<void>;
  selectItem: (item: MenuItem | null) => void;
  reset: () => void;
  getNavigableItems: () => MenuItem[];
  isLoading: boolean;
}

const initialState: MenuState = {
  status: 'idle',
  menu: null,
  selectedItem: null,
  error: null,
};

export function useMenu(): UseMenuReturn {
  const [state, setState] = useState<MenuState>(initialState);
  const [isLoading, setIsLoading] = useState(true);

  // Load persisted state on mount
  useEffect(() => {
    loadMenuState().then(savedState => {
      if (savedState) {
        setState({
          status: savedState.status,
          menu: savedState.menu,
          selectedItem: null,
          error: savedState.error,
        });
      }
      setIsLoading(false);
    });
  }, []);

  // Persist state changes
  useEffect(() => {
    if (!isLoading) {
      saveMenuState({
        status: state.status,
        menu: state.menu,
        error: state.error,
      });
    }
  }, [state.status, state.menu, state.error, isLoading]);

  const analyzeMenu = useCallback(async () => {
    setState(prev => ({ ...prev, status: 'analyzing', error: null }));

    try {
      // Get current tab
      const [tab] = await chrome.tabs.query({ active: true, currentWindow: true });
      
      if (!tab?.id) {
        throw new Error('');
      }

      // Send menu analysis request to content script
      const response = await chrome.tabs.sendMessage(tab.id, { 
        type: 'ANALYZE_MENU' 
      });

      if (response.success && response.menu) {
        setState({
          status: 'done',
          menu: response.menu,
          selectedItem: null,
          error: null,
        });
      } else {
        throw new Error(response.error || '');
      }
    } catch (error) {
      console.error('[useMenu] Analysis failed:', error);
      setState({
        status: 'error',
        menu: null,
        selectedItem: null,
        error: error instanceof Error ? error.message : '',
      });
    }
  }, []);

  const selectItem = useCallback((item: MenuItem | null) => {
    setState(prev => ({ ...prev, selectedItem: item }));
  }, []);

  const reset = useCallback(() => {
    setState(initialState);
    clearMenuState();
  }, []);

  const getNavigableItems = useCallback((): MenuItem[] => {
    if (!state.menu) return [];
    
    const navigable: MenuItem[] = [];
    
    function collect(items: MenuItem[]) {
      for (const item of items) {
        if (item.path && !item.path.startsWith('#') && !item.path.startsWith('javascript:')) {
          navigable.push(item);
        }
        if (item.children) {
          collect(item.children);
        }
      }
    }
    
    collect(state.menu.items);
    return navigable;
  }, [state.menu]);

  return {
    ...state,
    analyzeMenu,
    selectItem,
    reset,
    getNavigableItems,
    isLoading,
  };
}
