/**
 * Base Generator - Interface and base class for code generators
 */

import type { RecognizedComponent, ComponentType } from '../types/component';
import type { Page, LayoutInfo } from '../types/analysis';
import type { GeneratedCode, TemplateContext, TargetFramework } from '../types/project';

/**
 * Code generation options
 */
export interface GenerationOptions {
  framework: TargetFramework;
  typescript: boolean;
  useHooks: boolean;
  includeComments: boolean;
  indentSize: number;
}

/**
 * Default generation options
 */
export const DEFAULT_GENERATION_OPTIONS: GenerationOptions = {
  framework: 'react-antd',
  typescript: true,
  useHooks: true,
  includeComments: true,
  indentSize: 2,
};

/**
 * Framework adapter interface
 */
export interface FrameworkAdapter {
  /**
   * Framework identifier
   */
  framework: TargetFramework;
  
  /**
   * Generate code for a component
   */
  generateComponent(
    component: RecognizedComponent,
    options: GenerationOptions
  ): GeneratedCode;
  
  /**
   * Generate code for a page
   */
  generatePage(
    page: Page,
    options: GenerationOptions
  ): GeneratedCode[];
  
  /**
   * Generate layout component
   */
  generateLayout(
    layout: LayoutInfo,
    options: GenerationOptions
  ): GeneratedCode;
  
  /**
   * Get import statements for a component type
   */
  getImports(componentType: ComponentType): string[];
  
  /**
   * Get file extension for generated files
   */
  getFileExtension(options: GenerationOptions): string;
}

/**
 * Base generator class with common functionality
 */
export abstract class BaseGenerator implements FrameworkAdapter {
  abstract framework: TargetFramework;
  
  abstract generateComponent(
    component: RecognizedComponent,
    options: GenerationOptions
  ): GeneratedCode;
  
  abstract generatePage(
    page: Page,
    options: GenerationOptions
  ): GeneratedCode[];
  
  abstract generateLayout(
    layout: LayoutInfo,
    options: GenerationOptions
  ): GeneratedCode;
  
  abstract getImports(componentType: ComponentType): string[];
  
  /**
   * Get file extension based on options
   */
  getFileExtension(options: GenerationOptions): string {
    if (this.framework === 'react-antd') {
      return options.typescript ? '.tsx' : '.jsx';
    }
    return options.typescript ? '.ts' : '.js';
  }
  
  /**
   * Convert component name to valid identifier
   */
  protected toComponentName(name: string): string {
    return name
      .replace(/[^a-zA-Z0-9]/g, '')
      .replace(/^[0-9]/, '_$&');
  }
  
  /**
   * Generate indentation
   */
  protected indent(level: number, size: number = 2): string {
    return ' '.repeat(level * size);
  }
  
  /**
   * Create template context from component
   */
  protected createContext(
    component: RecognizedComponent,
    _options: GenerationOptions
  ): TemplateContext {
    return {
      componentName: this.toComponentName(component.name),
      componentType: component.type,
      config: component.config as Record<string, unknown>,
      imports: this.getImports(component.type),
    };
  }
}
