/**
 * React + Ant Design Adapter - Generate React code with Ant Design components
 */

import type { RecognizedComponent, ComponentType, TableConfig, FormConfig } from '../../types/component';
import type { Page, LayoutInfo } from '../../types/analysis';
import type { GeneratedCode, TargetFramework } from '../../types/project';
import { BaseGenerator, GenerationOptions } from '../base-generator';

/**
 * Ant Design component imports mapping
 */
const ANTD_IMPORTS: Record<ComponentType, string[]> = {
  table: ["import { Table } from 'antd';", "import type { ColumnsType } from 'antd/es/table';"],
  form: ["import { Form, Input, Button } from 'antd';"],
  button: ["import { Button } from 'antd';"],
  modal: ["import { Modal } from 'antd';"],
  card: ["import { Card } from 'antd';"],
  tabs: ["import { Tabs } from 'antd';"],
  menu: ["import { Menu } from 'antd';"],
  breadcrumb: ["import { Breadcrumb } from 'antd';"],
  pagination: ["import { Pagination } from 'antd';"],
  input: ["import { Input } from 'antd';"],
  select: ["import { Select } from 'antd';"],
  checkbox: ["import { Checkbox } from 'antd';"],
  radio: ["import { Radio } from 'antd';"],
  switch: ["import { Switch } from 'antd';"],
  datepicker: ["import { DatePicker } from 'antd';"],
  upload: ["import { Upload, Button } from 'antd';", "import { UploadOutlined } from '@ant-design/icons';"],
  tree: ["import { Tree } from 'antd';"],
  transfer: ["import { Transfer } from 'antd';"],
  unknown: [],
};

/**
 * React + Ant Design code generator
 */
export class ReactAntdAdapter extends BaseGenerator {
  framework: TargetFramework = 'react-antd';
  
  getImports(componentType: ComponentType): string[] {
    return ANTD_IMPORTS[componentType] || [];
  }
  
  /**
   * Generate table component code
   */
  private generateTableCode(component: RecognizedComponent, options: GenerationOptions): string {
    const config = component.config as TableConfig;
    const componentName = this.toComponentName(component.name);
    const { indentSize } = options;
    
    // Generate columns definition
    const columnsCode = this.generateColumnsCode(config.columns || [], indentSize);
    
    // Generate pagination props
    const paginationProps = config.pagination 
      ? `pagination={{ pageSize: ${config.pagination.pageSize}, showSizeChanger: ${config.pagination.showSizeChanger || false}, showTotal: ${config.pagination.showTotal || false} ? (total) => \` \${total} \` : undefined }}`
      : 'pagination={false}';
    
    return `import React, { useState, useEffect } from 'react';
import { Table, Button, Space } from 'antd';
import type { ColumnsType } from 'antd/es/table';

interface DataType {
  key: string;
  ${config.columns?.map(col => `${col.dataIndex}: string;`).join('\n  ') || ''}
}

interface ${componentName}Props {
  dataSource?: DataType[];
  loading?: boolean;
  onEdit?: (record: DataType) => void;
  onDelete?: (record: DataType) => void;
}

const ${componentName}: React.FC<${componentName}Props> = ({
  dataSource = [],
  loading = false,
  onEdit,
  onDelete,
}) => {
${columnsCode}

  return (
    <Table<DataType>
      columns={columns}
      dataSource={dataSource}
      loading={loading}
      rowKey="${config.rowKey || 'key'}"
      ${config.bordered ? 'bordered' : ''}
      ${paginationProps}
    />
  );
};

export default ${componentName};
`;
  }
  
  /**
   * Generate columns definition code
   */
  private generateColumnsCode(columns: TableConfig['columns'], indentSize: number): string {
    const indent = this.indent(1, indentSize);
    const indent2 = this.indent(2, indentSize);
    
    const columnsItems = columns.map(col => {
      const props: string[] = [
        `title: '${col.title}'`,
        `dataIndex: '${col.dataIndex}'`,
        `key: '${col.key}'`,
      ];
      
      if (col.width) props.push(`width: ${col.width}`);
      if (col.fixed) props.push(`fixed: '${col.fixed}'`);
      if (col.sorter) props.push(`sorter: true`);
      if (col.ellipsis) props.push(`ellipsis: true`);
      
      if (col.render === 'actions') {
        props.push(`render: (_, record) => (
${indent2}  <Space>
${indent2}    <Button type="link" onClick={() => onEdit?.(record)}></Button>
${indent2}    <Button type="link" danger onClick={() => onDelete?.(record)}></Button>
${indent2}  </Space>
${indent2})`);
      }
      
      return `${indent2}{ ${props.join(', ')} }`;
    });
    
    return `${indent}const columns: ColumnsType<DataType> = [
${columnsItems.join(',\n')},
${indent}];`;
  }
  
  /**
   * Generate form component code
   */
  private generateFormCode(component: RecognizedComponent, options: GenerationOptions): string {
    const config = component.config as FormConfig;
    const componentName = this.toComponentName(component.name);
    const { indentSize } = options;
    
    // Collect unique imports
    const formImports = new Set(['Form', 'Button']);
    config.fields?.forEach(field => {
      switch (field.type) {
        case 'input':
        case 'textarea':
          formImports.add('Input');
          break;
        case 'select':
          formImports.add('Select');
          break;
        case 'checkbox':
          formImports.add('Checkbox');
          break;
        case 'radio':
          formImports.add('Radio');
          break;
        case 'switch':
          formImports.add('Switch');
          break;
        case 'datepicker':
          formImports.add('DatePicker');
          break;
        case 'number':
          formImports.add('InputNumber');
          break;
        case 'upload':
          formImports.add('Upload');
          break;
      }
    });
    
    const importsList = Array.from(formImports).join(', ');
    
    // Generate form fields
    const fieldsCode = this.generateFormFieldsCode(config.fields || [], indentSize);
    
    // Generate form values interface
    const formValuesInterface = config.fields?.map(field => 
      `  ${field.name}${field.required ? '' : '?'}: ${this.getFieldTypeScript(field.type)};`
    ).join('\n') || '';
    
    return `import React from 'react';
import { ${importsList} } from 'antd';

interface FormValues {
${formValuesInterface}
}

interface ${componentName}Props {
  initialValues?: Partial<FormValues>;
  onSubmit?: (values: FormValues) => void;
  onCancel?: () => void;
  loading?: boolean;
}

const ${componentName}: React.FC<${componentName}Props> = ({
  initialValues,
  onSubmit,
  onCancel,
  loading = false,
}) => {
  const [form] = Form.useForm<FormValues>();

  const handleFinish = (values: FormValues) => {
    onSubmit?.(values);
  };

  return (
    <Form
      form={form}
      layout="${config.layout || 'horizontal'}"
      ${config.labelCol ? `labelCol={{ span: ${config.labelCol} }}` : ''}
      ${config.wrapperCol ? `wrapperCol={{ span: ${config.wrapperCol} }}` : ''}
      initialValues={initialValues}
      onFinish={handleFinish}
    >
${fieldsCode}

      <Form.Item wrapperCol={{ offset: ${config.labelCol || 6}, span: ${config.wrapperCol || 18} }}>
        <Button type="primary" htmlType="submit" loading={loading}>
          ${config.submitText || ''}
        </Button>
        {onCancel && (
          <Button style={{ marginLeft: 8 }} onClick={onCancel}>
            ${config.cancelText || ''}
          </Button>
        )}
      </Form.Item>
    </Form>
  );
};

export default ${componentName};
`;
  }
  
  /**
   * Generate form fields code
   */
  private generateFormFieldsCode(fields: FormConfig['fields'], indentSize: number): string {
    const indent = this.indent(3, indentSize);
    
    return fields.map(field => {
      const rules = field.required ? `rules={[{ required: true, message: '${field.label}' }]}` : '';
      const inputComponent = this.getInputComponent(field);
      
      return `${indent}<Form.Item
${indent}  name="${field.name}"
${indent}  label="${field.label}"
${indent}  ${rules}
${indent}>
${indent}  ${inputComponent}
${indent}</Form.Item>`;
    }).join('\n\n');
  }
  
  /**
   * Get input component JSX for field type
   */
  private getInputComponent(field: FormConfig['fields'][0]): string {
    const placeholder = field.placeholder ? `placeholder="${field.placeholder}"` : '';
    
    switch (field.type) {
      case 'textarea':
        return `<Input.TextArea ${placeholder} />`;
      case 'number':
        return `<InputNumber style={{ width: '100%' }} ${placeholder} />`;
      case 'select':
        const selectOptions = field.options?.map(opt => 
          `{ label: '${opt.label}', value: '${opt.value}' }`
        ).join(',\n            ') || '';
        return `<Select\n            ${placeholder}\n            options={[\n              ${selectOptions}\n            ]}\n          />`;
      case 'checkbox':
        return '<Checkbox />';
      case 'radio':
        const radioOptions = field.options?.map(opt =>
          `<Radio value="${opt.value}">${opt.label}</Radio>`
        ).join(' ') || '';
        return `<Radio.Group>${radioOptions}</Radio.Group>`;
      case 'switch':
        return '<Switch />';
      case 'datepicker':
        return '<DatePicker style={{ width: \'100%\' }} />';
      case 'upload':
        return '<Upload><Button></Button></Upload>';
      default:
        return `<Input ${placeholder} />`;
    }
  }
  
  /**
   * Get TypeScript type for field
   */
  private getFieldTypeScript(type: FormConfig['fields'][0]['type']): string {
    switch (type) {
      case 'checkbox':
      case 'switch':
        return 'boolean';
      case 'number':
        return 'number';
      case 'datepicker':
        return 'string | Date';
      case 'upload':
        return 'File[]';
      default:
        return 'string';
    }
  }
  
  generateComponent(component: RecognizedComponent, options: GenerationOptions): GeneratedCode {
    const componentName = this.toComponentName(component.name);
    const ext = this.getFileExtension(options);
    let content: string;
    
    switch (component.type) {
      case 'table':
        content = this.generateTableCode(component, options);
        break;
      case 'form':
        content = this.generateFormCode(component, options);
        break;
      default:
        content = this.generateGenericComponent(component, options);
    }
    
    return {
      filename: `${componentName}${ext}`,
      content,
      type: 'component',
      path: `src/components/${componentName}${ext}`,
    };
  }
  
  /**
   * Generate generic component for unsupported types
   */
  private generateGenericComponent(component: RecognizedComponent, _options: GenerationOptions): string {
    const componentName = this.toComponentName(component.name);
    
    return `import React from 'react';

interface ${componentName}Props {
  // Add props as needed
}

/**
 * ${component.name}
 * Component type: ${component.type}
 * Original CSS classes: ${component.cssClasses.join(', ')}
 */
const ${componentName}: React.FC<${componentName}Props> = (props) => {
  // TODO: Implement ${component.type} component
  return (
    <div className="${componentName.toLowerCase()}">
      {/* ${component.type} component placeholder */}
    </div>
  );
};

export default ${componentName};
`;
  }
  
  generatePage(page: Page, options: GenerationOptions): GeneratedCode[] {
    const codes: GeneratedCode[] = [];
    const ext = this.getFileExtension(options);
    
    // Generate component files
    for (const component of page.components) {
      codes.push(this.generateComponent(component, options));
    }
    
    // Generate page file that imports components
    const pageName = this.pathToPageName(page.path);
    const pageCode = this.generatePageFile(page, pageName, options);
    
    codes.push({
      filename: `${pageName}${ext}`,
      content: pageCode,
      type: 'page',
      path: `src/pages/${pageName}${ext}`,
    });
    
    return codes;
  }
  
  /**
   * Generate page file that assembles components
   */
  private generatePageFile(page: Page, pageName: string, _options: GenerationOptions): string {
    // Generate imports for each component
    const componentImports = page.components.map(comp => {
      const compName = this.toComponentName(comp.name);
      return `import ${compName} from '../components/${compName}';`;
    }).join('\n');
    
    // Generate component usage
    const componentUsage = page.components.map(comp => {
      const compName = this.toComponentName(comp.name);
      return `      <${compName} />`;
    }).join('\n');
    
    return `import React from 'react';
import { Card, Typography } from 'antd';
${componentImports}

const { Title } = Typography;

const ${pageName}: React.FC = () => {
  return (
    <div className="page-container">
      <Title level={2}>${page.title}</Title>
      <Card>
${componentUsage}
      </Card>
    </div>
  );
};

export default ${pageName};
`;
  }
  
  /**
   * Convert URL path to page name
   */
  private pathToPageName(path: string): string {
    const segments = path.split('/').filter(Boolean);
    const lastSegment = segments[segments.length - 1] || 'Index';
    
    return lastSegment
      .split(/[-_]/)
      .map(part => part.charAt(0).toUpperCase() + part.slice(1).toLowerCase())
      .join('') + 'Page';
  }
  
  generateLayout(layout: LayoutInfo, options: GenerationOptions): GeneratedCode {
    const ext = this.getFileExtension(options);
    
    const code = `import React from 'react';
import { Layout, Menu } from 'antd';

const { Header, Sider, Content, Footer } = Layout;

interface LayoutProps {
  children: React.ReactNode;
}

const AppLayout: React.FC<LayoutProps> = ({ children }) => {
  return (
    <Layout style={{ minHeight: '100vh' }}>
      ${layout.hasHeader ? `<Header style={{ height: ${layout.headerHeight || 64} }}>
        {/* Header content */}
      </Header>` : ''}
      <Layout>
        ${layout.hasSidebar ? `<Sider
          width={${layout.sidebarWidth || 200}}
          ${layout.sidebarPosition === 'right' ? "style={{ order: 1 }}" : ''}
        >
          <Menu mode="inline">
            {/* Menu items */}
          </Menu>
        </Sider>` : ''}
        <Content style={{ padding: '24px' }}>
          {children}
        </Content>
      </Layout>
      ${layout.hasFooter ? `<Footer style={{ textAlign: 'center' }}>
        {/* Footer content */}
      </Footer>` : ''}
    </Layout>
  );
};

export default AppLayout;
`;
    
    return {
      filename: `AppLayout${ext}`,
      content: code,
      type: 'layout',
      path: `src/layouts/AppLayout${ext}`,
    };
  }
}

// Export singleton instance
export const reactAntdAdapter = new ReactAntdAdapter();
