/**
 * SettingsPanel - Settings for tech stack and language
 */

import React, { useState, useEffect } from 'react';
import { Card, Space, Typography, Select, Divider, Tag } from 'antd';
import { 
  CodeOutlined,
  GlobalOutlined,
  CheckCircleOutlined,
  MailOutlined,
  GithubOutlined,
} from '@ant-design/icons';
import { useI18n, languageNames, Language } from '../i18n';

const { Text, Title, Link } = Typography;

const CONTACT_EMAIL = '270580156@qq.com';
const CONTACT_GITHUB = 'https://github.com/pengjinning/admin-clone';

export type TechStack = 'react-antd' | 'vue-element' | 'vue-antdv' | 'python-flask' | 'python-django';

interface TechStackOption {
  value: TechStack;
  labelKey: keyof typeof import('../i18n/locales').locales['zh-CN'];
  description: string;
  status: 'available' | 'coming-soon';
  icon: string;
}

const techStackOptions: TechStackOption[] = [
  { 
    value: 'react-antd', 
    labelKey: 'techReactAntd',
    description: 'TypeScript + Vite',
    status: 'available',
    icon: '',
  },
  { 
    value: 'vue-element', 
    labelKey: 'techVueElement',
    description: 'TypeScript + Vite',
    status: 'coming-soon',
    icon: '',
  },
  { 
    value: 'vue-antdv', 
    labelKey: 'techVueAntdv',
    description: 'TypeScript + Vite',
    status: 'coming-soon',
    icon: '',
  },
  { 
    value: 'python-flask', 
    labelKey: 'techPythonFlask',
    description: 'Jinja2 Templates',
    status: 'coming-soon',
    icon: '',
  },
  { 
    value: 'python-django', 
    labelKey: 'techPythonDjango',
    description: 'Django Templates',
    status: 'coming-soon',
    icon: '',
  },
];

const TECH_STACK_STORAGE_KEY = 'admin-clone-tech-stack';

interface SettingsPanelProps {
  onTechStackChange?: (stack: TechStack) => void;
}

const SettingsPanel: React.FC<SettingsPanelProps> = ({ onTechStackChange }) => {
  const { language, setLanguage, t } = useI18n();
  const [techStack, setTechStack] = useState<TechStack>('react-antd');

  // Load saved tech stack
  useEffect(() => {
    chrome.storage.local.get([TECH_STACK_STORAGE_KEY], (result) => {
      if (result[TECH_STACK_STORAGE_KEY]) {
        setTechStack(result[TECH_STACK_STORAGE_KEY] as TechStack);
      }
    });
  }, []);

  // Handle tech stack change
  const handleTechStackChange = (value: TechStack) => {
    setTechStack(value);
    chrome.storage.local.set({ [TECH_STACK_STORAGE_KEY]: value });
    onTechStackChange?.(value);
  };

  // Handle language change
  const handleLanguageChange = (value: Language) => {
    setLanguage(value);
  };

  return (
    <Card size="small" bordered={false}>
      <Space direction="vertical" style={{ width: '100%' }} size="large">
        {/* Tech Stack Settings */}
        <div>
          <Space align="center" style={{ marginBottom: 8 }}>
            <CodeOutlined style={{ fontSize: 16, color: '#1890ff' }} />
            <Title level={5} style={{ margin: 0 }}>{t.techStackSettings}</Title>
          </Space>
          <Text type="secondary" style={{ display: 'block', marginBottom: 12, fontSize: 12 }}>
            {t.techStackDescription}
          </Text>
          
          <Select
            value={techStack}
            onChange={handleTechStackChange}
            style={{ width: '100%' }}
            placeholder={t.selectTechStack}
            options={techStackOptions.map(option => ({
              value: option.value,
              disabled: option.status === 'coming-soon',
              label: (
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                  <Space>
                    <span style={{ fontSize: 16 }}>{option.icon}</span>
                    <div>
                      <div>{t[option.labelKey]}</div>
                      <Text type="secondary" style={{ fontSize: 11 }}>{option.description}</Text>
                    </div>
                  </Space>
                  {option.status === 'available' ? (
                    <CheckCircleOutlined style={{ color: '#52c41a' }} />
                  ) : (
                    <Tag color="orange" style={{ fontSize: 10 }}>Coming Soon</Tag>
                  )}
                </div>
              ),
              displayLabel: (
                <Space>
                  <span>{option.icon}</span>
                  <span>{t[option.labelKey]}</span>
                </Space>
              ),
            }))}
            optionLabelProp="displayLabel"
          />
          
          {/* Current selection display */}
          <div style={{ 
            marginTop: 12, 
            padding: '8px 12px', 
            background: '#f6ffed', 
            borderRadius: 4,
            border: '1px solid #b7eb8f',
          }}>
            <Space>
              <CheckCircleOutlined style={{ color: '#52c41a' }} />
              <Text>
                {language === 'en' ? 'Current: ' : ''}
                <Text strong>
                  {techStackOptions.find(o => o.value === techStack)?.icon}{' '}
                  {t[techStackOptions.find(o => o.value === techStack)?.labelKey || 'techReactAntd']}
                </Text>
              </Text>
            </Space>
          </div>
        </div>

        <Divider style={{ margin: '8px 0' }} />

        {/* Language Settings */}
        <div>
          <Space align="center" style={{ marginBottom: 8 }}>
            <GlobalOutlined style={{ fontSize: 16, color: '#1890ff' }} />
            <Title level={5} style={{ margin: 0 }}>{t.languageSettings}</Title>
          </Space>
          <Text type="secondary" style={{ display: 'block', marginBottom: 12, fontSize: 12 }}>
            {t.languageDescription}
          </Text>
          
          <Select
            value={language}
            onChange={handleLanguageChange}
            style={{ width: '100%' }}
            placeholder={t.selectLanguage}
            options={(Object.keys(languageNames) as Language[]).map(lang => ({
              value: lang,
              label: (
                <Space>
                  <span>{lang === 'zh-CN' ? '' : lang === 'zh-TW' ? '' : ''}</span>
                  <span>{languageNames[lang]}</span>
                </Space>
              ),
            }))}
          />
        </div>

        <Divider style={{ margin: '8px 0' }} />

        {/* Contact Info */}
        <div>
          <Space align="center" style={{ marginBottom: 8 }}>
            <MailOutlined style={{ fontSize: 16, color: '#1890ff' }} />
            <Title level={5} style={{ margin: 0 }}>{t.contactSectionTitle}</Title>
          </Space>
          <Space direction="vertical" size={6} style={{ width: '100%' }}>
            <Space size={6}>
              <MailOutlined style={{ color: '#1890ff' }} />
              <Text strong>{t.contactEmailLabel}:</Text>
            </Space>
            <Link href={`mailto:${CONTACT_EMAIL}`}>
              {CONTACT_EMAIL}
            </Link>
            <Space size={6}>
              <GithubOutlined />
              <Text strong>{t.contactGitHubLabel}:</Text>
            </Space>
            <Link href={CONTACT_GITHUB} target="_blank" rel="noreferrer">
              {CONTACT_GITHUB}
            </Link>
          </Space>
        </div>

        <Divider style={{ margin: '8px 0' }} />

        {/* Info */}
        <div style={{ 
          padding: '8px 12px', 
          background: '#f5f5f5', 
          borderRadius: 4,
          fontSize: 11,
        }}>
          <Space direction="vertical" size={4}>
            <Text type="secondary">
              {t.settingsDisclaimer}
            </Text>
            <Text type="secondary">
              {t.autoInteractionHint}
            </Text>
            <Text type="secondary">
              {language === 'en' 
                ? 'More tech stacks will be supported in future versions. Stay tuned!'
                : language === 'zh-TW'
                ? ''
                : ''
              }
            </Text>
          </Space>
        </div>
      </Space>
    </Card>
  );
};

export default SettingsPanel;
