/**
 * useAnalysis Hook - Manage page analysis state
 */

import { useState, useCallback, useEffect } from 'react';
import type { Page, AnalysisResult } from '@core/types/analysis';
import type { RecognizedComponent } from '@core/types/component';
import type { GeneratedCode } from '@core/types/project';
import { generateFullPageCode } from '@core/generators/component-generator';
import { saveAnalysisState, loadAnalysisState, clearAnalysisState } from '../utils/state-storage';

export interface AnalysisState {
  status: 'idle' | 'analyzing' | 'generating' | 'done' | 'error';
  page: Page | null;
  generatedCodes: GeneratedCode[];
  error: string | null;
  progress: number;
}

export interface UseAnalysisReturn extends AnalysisState {
  analyzePage: () => Promise<void>;
  generateCode: () => void;
  reset: () => void;
  selectComponent: (component: RecognizedComponent | null) => void;
  selectedComponent: RecognizedComponent | null;
  isLoading: boolean;
}

const initialState: AnalysisState = {
  status: 'idle',
  page: null,
  generatedCodes: [],
  error: null,
  progress: 0,
};

export function useAnalysis(): UseAnalysisReturn {
  const [state, setState] = useState<AnalysisState>(initialState);
  const [selectedComponent, setSelectedComponent] = useState<RecognizedComponent | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  // Load persisted state on mount
  useEffect(() => {
    loadAnalysisState().then(savedState => {
      if (savedState) {
        setState({
          status: savedState.status,
          page: savedState.page,
          generatedCodes: savedState.generatedCodes,
          error: savedState.error,
          progress: savedState.progress,
        });
      }
      setIsLoading(false);
    });
  }, []);

  // Persist state changes
  useEffect(() => {
    if (!isLoading) {
      saveAnalysisState({
        status: state.status,
        page: state.page,
        generatedCodes: state.generatedCodes,
        error: state.error,
        progress: state.progress,
      });
    }
  }, [state, isLoading]);

  const analyzePage = useCallback(async () => {
    setState(prev => ({ ...prev, status: 'analyzing', error: null, progress: 10 }));

    try {
      // Get current tab
      const [tab] = await chrome.tabs.query({ active: true, currentWindow: true });
      
      if (!tab?.id) {
        throw new Error('');
      }

      // Check if URL is valid for content script injection
      const url = tab.url || '';
      if (url.startsWith('chrome://') || url.startsWith('chrome-extension://') || url.startsWith('about:') || url.startsWith('edge://')) {
        throw new Error('');
      }

      setState(prev => ({ ...prev, progress: 20 }));

      // Try to ping the content script first
      let contentScriptReady = false;
      try {
        const pingResponse = await chrome.tabs.sendMessage(tab.id, { type: 'GET_PAGE_INFO' });
        contentScriptReady = !!pingResponse;
      } catch {
        contentScriptReady = false;
      }

      // If content script is not ready, show helpful message
      if (!contentScriptReady) {
        throw new Error(' Chrome ');
      }

      setState(prev => ({ ...prev, progress: 30 }));

      // Send analysis request to content script
      let response: { type: string; result: AnalysisResult };
      try {
        response = await chrome.tabs.sendMessage(tab.id, { 
          type: 'ANALYZE_PAGE' 
        }) as { type: string; result: AnalysisResult };
      } catch (msgError) {
        console.error('[useAnalysis] Message failed:', msgError);
        throw new Error('');
      }

      setState(prev => ({ ...prev, progress: 70 }));

      if (response.result?.success && response.result.page) {
        const page = response.result.page;
        
        // Generate code for the analyzed page
        setState(prev => ({ ...prev, status: 'generating', progress: 80 }));
        
        const codes = generateFullPageCode(page);
        
        setState({
          status: 'done',
          page,
          generatedCodes: codes,
          error: null,
          progress: 100,
        });
      } else {
        throw new Error(response.result?.error || '');
      }
    } catch (error) {
      console.error('[useAnalysis] Analysis failed:', error);
      setState({
        status: 'error',
        page: null,
        generatedCodes: [],
        error: error instanceof Error ? error.message : '',
        progress: 0,
      });
    }
  }, []);

  const generateCode = useCallback(() => {
    if (!state.page) return;
    
    setState(prev => ({ ...prev, status: 'generating' }));
    
    try {
      const codes = generateFullPageCode(state.page);
      setState(prev => ({
        ...prev,
        status: 'done',
        generatedCodes: codes,
      }));
    } catch (error) {
      setState(prev => ({
        ...prev,
        status: 'error',
        error: error instanceof Error ? error.message : '',
      }));
    }
  }, [state.page]);

  const reset = useCallback(() => {
    setState(initialState);
    setSelectedComponent(null);
    clearAnalysisState();
  }, []);

  const selectComponent = useCallback((component: RecognizedComponent | null) => {
    setSelectedComponent(component);
  }, []);

  return {
    ...state,
    analyzePage,
    generateCode,
    reset,
    selectComponent,
    selectedComponent,
    isLoading,
  };
}
