  /**
   * Generate App.tsx
   */
  private async generateAppTsx(menu?: Menu): Promise<void> {
    const hasMenu = menu && menu.items.length > 0;
    
    const content = `import { Routes, Route } from 'react-router-dom';
import MainLayout from '@layouts/MainLayout';
import routes from './router';

function App() {
  return (
    <Routes>
      <Route path="/" element={<MainLayout${hasMenu ? '' : ' collapsed={false}'} />}>
        {routes.map((route) => (
          <Route
            key={route.path}
            path={route.path}
            element={<route.component />}
          />
        ))}
      </Route>
    </Routes>
  );
}

export default App;
`;
    
    this.addFile('src', 'App.tsx', content, 'component');
  }
  
