  /**
   * Generate router config
   */
  private async generateRouter(pages: Page[], menu?: Menu): Promise<void> {
    const routes: Array<{ path: string; component: string; title: string }> = [];
    
    // Add home route
    routes.push({
      path: '/',
      component: 'HomePage',
      title: '',
    });
    
    // Generate routes from menu
    if (menu) {
      const addRoutes = (items: MenuItem[]) => {
        for (const item of items) {
          if (item.path && item.path !== '#') {
            const componentName = this.pathToComponentName(item.path);
            if (!routes.find(r => r.path === item.path)) {
              routes.push({
                path: item.path,
                component: componentName,
                title: item.title,
              });
            }
          }
          if (item.children) {
            addRoutes(item.children);
          }
        }
      };
      addRoutes(menu.items);
    }
    
    // Add routes from analyzed pages
    for (const page of pages) {
      const path = page.path || `/${page.id}`;
      if (!routes.find(r => r.path === path)) {
        const componentName = this.pathToComponentName(path);
        routes.push({
          path,
          component: componentName,
          title: page.title,
        });
      }
    }
    
    // Generate imports and route array
    const imports = routes.map(r => 
      `import ${r.component} from '@pages/${r.component}';`
    ).join('\n');
    
    const routeArray = routes.map(r => `  {
    path: '${r.path}',
    component: ${r.component},
    title: '${r.title}',
  }`).join(',\n');
    
    const content = `import { lazy } from 'react';

${imports}

export interface RouteConfig {
  path: string;
  component: React.LazyExoticComponent<React.FC> | React.FC;
  title: string;
  children?: RouteConfig[];
}

const routes: RouteConfig[] = [
${routeArray}
];

export default routes;
`;
    
    this.addFile('src', 'router.tsx', content, 'route');
  }
  
  /**
   * Generate menu config
   */
  private async generateMenuConfig(menu: Menu): Promise<void> {
    const generateMenuItems = (items: MenuItem[]): RouteMenuItem[] => {
      return items.map(item => ({
        key: item.path || `#${item.id}`,
        path: item.path || '',
        title: item.title,
        icon: item.icon,
        children: item.children ? generateMenuItems(item.children) : undefined,
      }));
    };
    
    const menuItems = generateMenuItems(menu.items);
    
    const generateAntdMenuItems = (items: RouteMenuItem[], indent: number = 2): string => {
      const spaces = '  '.repeat(indent);
      return items.map(item => {
        const iconComponent = item.icon ? this.cssClassToIcon(item.icon) : 'AppstoreOutlined';
        const hasChildren = item.children && item.children.length > 0;
        
        if (hasChildren) {
          return `${spaces}{
${spaces}  key: '${item.key}',
${spaces}  icon: <${iconComponent} />,
${spaces}  label: '${item.title}',
${spaces}  children: [
${generateAntdMenuItems(item.children!, indent + 2)}
${spaces}  ],
${spaces}}`;
        } else {
          return `${spaces}{
${spaces}  key: '${item.key}',
${spaces}  icon: <${iconComponent} />,
${spaces}  label: '${item.title}',
${spaces}}`;
        }
      }).join(',\n');
    };
    
    // Collect all icons used
    const collectIcons = (items: RouteMenuItem[]): Set<string> => {
      const icons = new Set<string>();
      for (const item of items) {
        icons.add(item.icon ? this.cssClassToIcon(item.icon) : 'AppstoreOutlined');
        if (item.children) {
          collectIcons(item.children).forEach(i => icons.add(i));
        }
      }
      return icons;
    };
    
    const usedIcons = collectIcons(menuItems);
    const iconImports = Array.from(usedIcons).join(',\n  ');
    
    const content = `import type { MenuProps } from 'antd';
import {
  ${iconImports}
} from '@ant-design/icons';

type MenuItem = Required<MenuProps>['items'][number];

export const menuConfig: MenuItem[] = [
${generateAntdMenuItems(menuItems)}
];

export const menuData = ${JSON.stringify(menuItems, null, 2)};
`;
    
    this.addFile('src/config', 'menu.tsx', content, 'config');
  }
  
  /**
   * Generate page component
   */
  private async generatePageComponent(page: Page): Promise<void> {
    const componentName = this.pathToComponentName(page.path || `/${page.id}`);
    
    // Collect component imports based on detected components
    const imports = new Set<string>();
    imports.add('Typography');
    imports.add('Space');
    
    for (const comp of page.components) {
      switch (comp.type) {
        case 'table':
          imports.add('Table');
          imports.add('Button');
          break;
        case 'form':
          imports.add('Form');
          imports.add('Input');
          imports.add('Button');
          break;
        case 'card':
          imports.add('Card');
          break;
        case 'button':
          imports.add('Button');
          break;
        case 'modal':
          imports.add('Modal');
          break;
        case 'tabs':
          imports.add('Tabs');
          break;
      }
    }
    
    const importList = Array.from(imports).join(', ');
    
    // Generate component content based on detected components
    let componentContent = '';
    
    const tableComp = page.components.find(c => c.type === 'table');
    if (tableComp) {
      componentContent += this.generateTableCode(tableComp);
    }
    
    const formComp = page.components.find(c => c.type === 'form');
    if (formComp) {
      componentContent += this.generateFormCode(formComp);
    }
    
    if (!componentContent) {
      componentContent = `      <Typography.Text type="secondary">
        ...
      </Typography.Text>`;
    }
    
    const content = `import React from 'react';
import { ${importList} } from 'antd';

const { Title } = Typography;

const ${componentName}: React.FC = () => {
  return (
    <Space direction="vertical" style={{ width: '100%' }} size="large">
      <Title level={4}>${page.title || componentName}</Title>
${componentContent}
    </Space>
  );
};

export default ${componentName};
`;
    
    this.addFile('src/pages', `${componentName}.tsx`, content, 'page', page.id);
  }
  
  /**
   * Generate table code
   */
  private generateTableCode(component: { config: unknown }): string {
    const config = component.config as { columns?: Array<{ title: string; key: string }> };
    const columns = config.columns || [
      { title: 'ID', key: 'id' },
      { title: '', key: 'name' },
      { title: '', key: 'status' },
      { title: '', key: 'action' },
    ];
    
    const columnsCode = columns.map(col => {
      if (col.key === 'action') {
        return `    {
      title: '${col.title}',
      key: 'action',
      render: () => (
        <Space>
          <Button type="link" size="small"></Button>
          <Button type="link" size="small" danger></Button>
        </Space>
      ),
    }`;
      }
      return `    { title: '${col.title}', dataIndex: '${col.key}', key: '${col.key}' }`;
    }).join(',\n');
    
    return `      <Table
        columns={[
${columnsCode}
        ]}
        dataSource={[]}
        pagination={{ pageSize: 10 }}
      />
`;
  }
  
  /**
   * Generate form code
   */
  private generateFormCode(component: { config: unknown }): string {
    const config = component.config as { fields?: Array<{ label: string; name: string; type: string }> };
    const fields = config.fields || [
      { label: '', name: 'name', type: 'input' },
      { label: '', name: 'description', type: 'textarea' },
    ];
    
    const fieldsCode = fields.map(field => {
      return `        <Form.Item label="${field.label}" name="${field.name}">
          <Input ${field.type === 'textarea' ? 'as={Input.TextArea} ' : ''}placeholder="${field.label}" />
        </Form.Item>`;
    }).join('\n');
    
    return `      <Card title="">
        <Form layout="vertical">
${fieldsCode}
          <Form.Item>
            <Button type="primary"></Button>
          </Form.Item>
        </Form>
      </Card>
`;
  }
  
