/**
 * MenuTree Component - Display analyzed menu structure
 */

import React, { useMemo } from 'react';
import { Tree, Empty, Typography, Tag, Space, Tooltip } from 'antd';
import type { TreeDataNode } from 'antd';
import { 
  FolderOutlined, 
  FileOutlined, 
  LinkOutlined,
} from '@ant-design/icons';
import type { MenuItem, Menu } from '@core/types/menu';

const { Text } = Typography;

interface MenuTreeProps {
  menu: Menu | null;
  selectedKeys?: string[];
  onSelect?: (item: MenuItem) => void;
  showPath?: boolean;
  showStatus?: boolean;
}

/**
 * Convert MenuItem to Tree DataNode
 */
function menuItemToTreeNode(item: MenuItem, showPath: boolean, showStatus: boolean): TreeDataNode {
  const hasChildren = item.children && item.children.length > 0;
  const hasPath = Boolean(item.path);
  
  const title = (
    <Space size={4}>
      <span>{item.title}</span>
      {showPath && hasPath && (
        <Tooltip title={item.path}>
          <LinkOutlined style={{ color: '#1890ff', fontSize: 12 }} />
        </Tooltip>
      )}
      {showStatus && item.isActive && (
        <Tag color="green" style={{ marginLeft: 4, fontSize: 10, lineHeight: '16px', padding: '0 4px' }}>
          
        </Tag>
      )}
      {item.icon && (
        <Text type="secondary" style={{ fontSize: 10 }}>
          [{item.icon}]
        </Text>
      )}
    </Space>
  );
  
  const node: TreeDataNode = {
    key: item.id,
    title,
    icon: hasChildren ? <FolderOutlined /> : <FileOutlined />,
    children: item.children?.map(child => menuItemToTreeNode(child, showPath, showStatus)),
  };
  
  return node;
}

const MenuTree: React.FC<MenuTreeProps> = ({
  menu,
  selectedKeys = [],
  onSelect,
  showPath = true,
  showStatus = true,
}) => {
  // Convert menu items to tree data
  const treeData = useMemo<TreeDataNode[]>(() => {
    if (!menu || !menu.items) return [];
    return menu.items.map(item => menuItemToTreeNode(item, showPath, showStatus));
  }, [menu, showPath, showStatus]);

  // Find menu item by id
  const findMenuItem = (id: string, items: MenuItem[]): MenuItem | null => {
    for (const item of items) {
      if (item.id === id) return item;
      if (item.children) {
        const found = findMenuItem(id, item.children);
        if (found) return found;
      }
    }
    return null;
  };

  // Handle tree node select
  const handleSelect = (keys: React.Key[]) => {
    if (keys.length > 0 && menu && onSelect) {
      const item = findMenuItem(keys[0] as string, menu.items);
      if (item) {
        onSelect(item);
      }
    }
  };

  if (!menu || treeData.length === 0) {
    return (
      <Empty 
        description="" 
        image={Empty.PRESENTED_IMAGE_SIMPLE}
        style={{ padding: 24 }}
      />
    );
  }

  // Count statistics
  const totalItems = countItems(menu.items);
  const navigableItems = countNavigableItems(menu.items);

  return (
    <div className="menu-tree">
      {/* Statistics */}
      <div style={{ 
        padding: '8px 12px', 
        background: '#fafafa', 
        borderRadius: 4,
        marginBottom: 12,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
      }}>
        <Space>
          <Text type="secondary">:</Text>
          <Tag>{menu.type === 'sidebar' ? '' : menu.type === 'horizontal' ? '' : ''}</Tag>
        </Space>
        <Space split={<Text type="secondary">|</Text>}>
          <Text type="secondary">
             <Text strong>{totalItems}</Text> 
          </Text>
          <Text type="secondary">
             <Text strong style={{ color: '#1890ff' }}>{navigableItems}</Text> 
          </Text>
        </Space>
      </div>

      {/* Tree */}
      <Tree
        treeData={treeData}
        selectedKeys={selectedKeys}
        onSelect={handleSelect}
        showIcon
        defaultExpandAll
        blockNode
        style={{ 
          background: 'transparent',
        }}
      />
    </div>
  );
};

/**
 * Count total menu items
 */
function countItems(items: MenuItem[]): number {
  let count = items.length;
  for (const item of items) {
    if (item.children) {
      count += countItems(item.children);
    }
  }
  return count;
}

/**
 * Count navigable items (with paths)
 */
function countNavigableItems(items: MenuItem[]): number {
  let count = 0;
  for (const item of items) {
    if (item.path && !item.path.startsWith('#') && !item.path.startsWith('javascript:')) {
      count++;
    }
    if (item.children) {
      count += countNavigableItems(item.children);
    }
  }
  return count;
}

export default MenuTree;
