/**
 * Component Generator - Orchestrates code generation for components
 */

import type { RecognizedComponent, ComponentType } from '../types/component';
import type { Page, LayoutInfo } from '../types/analysis';
import type { GeneratedCode, TargetFramework } from '../types/project';
import { 
  FrameworkAdapter, 
  GenerationOptions, 
  DEFAULT_GENERATION_OPTIONS 
} from './base-generator';
import { reactAntdAdapter } from './adapters/react-antd-adapter';

/**
 * Registry of framework adapters
 */
const adapters: Map<TargetFramework, FrameworkAdapter> = new Map([
  ['react-antd', reactAntdAdapter],
]);

/**
 * Get adapter for framework
 */
export function getAdapter(framework: TargetFramework): FrameworkAdapter | undefined {
  return adapters.get(framework);
}

/**
 * Register a new adapter
 */
export function registerAdapter(adapter: FrameworkAdapter): void {
  adapters.set(adapter.framework, adapter);
}

/**
 * Generate code for a single component
 */
export function generateComponentCode(
  component: RecognizedComponent,
  options: Partial<GenerationOptions> = {}
): GeneratedCode | null {
  const fullOptions: GenerationOptions = {
    ...DEFAULT_GENERATION_OPTIONS,
    ...options,
  };
  
  const adapter = getAdapter(fullOptions.framework);
  if (!adapter) {
    console.error(`No adapter found for framework: ${fullOptions.framework}`);
    return null;
  }
  
  return adapter.generateComponent(component, fullOptions);
}

/**
 * Generate code for multiple components
 */
export function generateComponentsCode(
  components: RecognizedComponent[],
  options: Partial<GenerationOptions> = {}
): GeneratedCode[] {
  const codes: GeneratedCode[] = [];
  
  for (const component of components) {
    const code = generateComponentCode(component, options);
    if (code) {
      codes.push(code);
    }
  }
  
  return codes;
}

/**
 * Generate code for a page
 */
export function generatePageCode(
  page: Page,
  options: Partial<GenerationOptions> = {}
): GeneratedCode[] {
  const fullOptions: GenerationOptions = {
    ...DEFAULT_GENERATION_OPTIONS,
    ...options,
  };
  
  const adapter = getAdapter(fullOptions.framework);
  if (!adapter) {
    console.error(`No adapter found for framework: ${fullOptions.framework}`);
    return [];
  }
  
  return adapter.generatePage(page, fullOptions);
}

/**
 * Generate layout code
 */
export function generateLayoutCode(
  layout: LayoutInfo,
  options: Partial<GenerationOptions> = {}
): GeneratedCode | null {
  const fullOptions: GenerationOptions = {
    ...DEFAULT_GENERATION_OPTIONS,
    ...options,
  };
  
  const adapter = getAdapter(fullOptions.framework);
  if (!adapter) {
    console.error(`No adapter found for framework: ${fullOptions.framework}`);
    return null;
  }
  
  return adapter.generateLayout(layout, fullOptions);
}

/**
 * Get imports for a component type
 */
export function getComponentImports(
  componentType: ComponentType,
  framework: TargetFramework = 'react-antd'
): string[] {
  const adapter = getAdapter(framework);
  if (!adapter) {
    return [];
  }
  
  return adapter.getImports(componentType);
}

/**
 * Generate all code for a page including layout
 */
export function generateFullPageCode(
  page: Page,
  options: Partial<GenerationOptions> = {}
): GeneratedCode[] {
  const codes: GeneratedCode[] = [];
  
  // Generate layout
  const layoutCode = generateLayoutCode(page.layout, options);
  if (layoutCode) {
    codes.push(layoutCode);
  }
  
  // Generate page and components
  const pageCodes = generatePageCode(page, options);
  codes.push(...pageCodes);
  
  return codes;
}
