/**
 * ComponentList - Display recognized components
 */

import React from 'react';
import { List, Tag, Typography, Empty, Tooltip } from 'antd';
import {
  TableOutlined,
  FormOutlined,
  PlusSquareOutlined,
  AppstoreOutlined,
  MenuOutlined,
  CreditCardOutlined,
  SwitcherOutlined,
} from '@ant-design/icons';
import type { RecognizedComponent, ComponentType } from '@core/types/component';

const { Text } = Typography;

interface ComponentListProps {
  components: RecognizedComponent[];
  selectedComponent: RecognizedComponent | null;
  onSelect: (component: RecognizedComponent) => void;
}

/**
 * Icon mapping for component types
 */
const COMPONENT_ICONS: Record<ComponentType, React.ReactNode> = {
  table: <TableOutlined />,
  form: <FormOutlined />,
  button: <PlusSquareOutlined />,
  modal: <AppstoreOutlined />,
  card: <CreditCardOutlined />,
  tabs: <SwitcherOutlined />,
  menu: <MenuOutlined />,
  breadcrumb: <MenuOutlined />,
  pagination: <AppstoreOutlined />,
  input: <FormOutlined />,
  select: <FormOutlined />,
  checkbox: <FormOutlined />,
  radio: <FormOutlined />,
  switch: <FormOutlined />,
  datepicker: <FormOutlined />,
  upload: <FormOutlined />,
  tree: <MenuOutlined />,
  transfer: <AppstoreOutlined />,
  unknown: <AppstoreOutlined />,
};

/**
 * Color mapping for component types
 */
const COMPONENT_COLORS: Record<ComponentType, string> = {
  table: 'blue',
  form: 'green',
  button: 'orange',
  modal: 'purple',
  card: 'cyan',
  tabs: 'magenta',
  menu: 'geekblue',
  breadcrumb: 'gold',
  pagination: 'lime',
  input: 'green',
  select: 'green',
  checkbox: 'green',
  radio: 'green',
  switch: 'green',
  datepicker: 'green',
  upload: 'green',
  tree: 'geekblue',
  transfer: 'purple',
  unknown: 'default',
};

/**
 * Format confidence as percentage
 */
function formatConfidence(confidence: number): string {
  return `${Math.round(confidence * 100)}%`;
}

const ComponentList: React.FC<ComponentListProps> = ({
  components,
  selectedComponent,
  onSelect,
}) => {
  if (components.length === 0) {
    return (
      <Empty
        image={Empty.PRESENTED_IMAGE_SIMPLE}
        description=""
      />
    );
  }

  return (
    <List
      size="small"
      dataSource={components}
      renderItem={(component) => {
        const isSelected = selectedComponent?.id === component.id;
        
        return (
          <List.Item
            onClick={() => onSelect(component)}
            style={{
              cursor: 'pointer',
              backgroundColor: isSelected ? '#e6f7ff' : undefined,
              borderRadius: 4,
              padding: '8px 12px',
              marginBottom: 4,
            }}
          >
            <List.Item.Meta
              avatar={
                <span style={{ fontSize: 18 }}>
                  {COMPONENT_ICONS[component.type]}
                </span>
              }
              title={
                <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
                  <Text strong>{component.name}</Text>
                  <Tag color={COMPONENT_COLORS[component.type]} style={{ margin: 0 }}>
                    {component.type}
                  </Tag>
                </div>
              }
              description={
                <Tooltip title={`: ${formatConfidence(component.confidence)}`}>
                  <Text type="secondary" style={{ fontSize: 12 }}>
                    {component.cssClasses.slice(0, 2).join(', ')}
                    {component.cssClasses.length > 2 && '...'}
                  </Text>
                </Tooltip>
              }
            />
          </List.Item>
        );
      }}
    />
  );
};

export default ComponentList;
