/**
 * AnalysisPanel - Main control panel for page analysis
 */

import React, { useEffect, useRef, useState } from 'react';
import { Card, Button, Space, Progress, Alert, Typography, Divider } from 'antd';
import { 
  PlayCircleOutlined,
  ReloadOutlined,
  CodeOutlined,
  CheckCircleOutlined,
  LoadingOutlined,
  PauseCircleOutlined,
  StopOutlined,
} from '@ant-design/icons';
import type { UseAnalysisReturn } from '../hooks/useAnalysis';
import type { UseMenuReturn } from '../hooks/useMenu';
import type { UseTraversalReturn } from '../hooks/useTraversal';
import ProgressBar from './ProgressBar';
import ExportButton from './ExportButton';
import { useI18n } from '../i18n';

const { Text } = Typography;

// Count total menu items including children
function countTotalMenuItems(items: any[]): number {
  let count = items.length;
  for (const item of items) {
    if (item.children) {
      count += countTotalMenuItems(item.children);
    }
  }
  return count;
}

interface AnalysisPanelProps {
  analysis: UseAnalysisReturn;
  menuState?: UseMenuReturn;
  traversalState?: UseTraversalReturn;
  sessionId?: string;
  onViewCode?: () => void;
}

const AnalysisPanel: React.FC<AnalysisPanelProps> = ({ analysis, menuState, traversalState, sessionId, onViewCode }) => {
  const { t } = useI18n();
  const { 
    page, 
    error, 
    reset,
  } = analysis;

  // Track if we should auto-start traversal after menu analysis
  const [isStartingAnalysis, setIsStartingAnalysis] = useState(false);
  const autoStartTraversalRef = useRef(false);
  
  // Menu state
  const isMenuAnalyzing = menuState?.status === 'analyzing';
  const isMenuDone = menuState?.status === 'done';
  const menuError = menuState?.status === 'error' ? menuState.error : null;
  
  // Traversal state
  const isTraversing = traversalState?.status === 'running';
  const isTraversalPaused = traversalState?.status === 'paused';
  const isTraversalCompleted = traversalState?.status === 'completed';
  const isTraversalInProgress = isTraversing || isTraversalPaused;
  const hasTraversedPages = traversalState?.analyzedPages && traversalState.analyzedPages.length > 0;

  // Combined status
  const isIdle = !isMenuAnalyzing && !isTraversalInProgress && !isTraversalCompleted && !hasTraversedPages;

  // Auto-start traversal when menu analysis completes
  useEffect(() => {
    if (autoStartTraversalRef.current && isMenuDone && menuState?.menu && traversalState) {
      autoStartTraversalRef.current = false;
      setIsStartingAnalysis(false);
      // Start traversal automatically
      traversalState.startTraversal(menuState.menu);
    }
  }, [isMenuDone, menuState?.menu, traversalState]);

  // Handle start analysis button click
  const handleStartAnalysis = async () => {
    if (!menuState) return;
    
    setIsStartingAnalysis(true);
    autoStartTraversalRef.current = true;
    
    // Start menu analysis, traversal will auto-start when done
    menuState.analyzeMenu();
  };

  // Handle reset
  const handleReset = () => {
    setIsStartingAnalysis(false);
    autoStartTraversalRef.current = false;
    menuState?.reset();
    traversalState?.reset();
    reset();
  };

  // Get current status text
  const getStatusText = () => {
    if (isMenuAnalyzing) return t.statusAnalyzingMenu;
    if (isTraversing) return t.statusTraversing;
    if (isTraversalPaused) return t.statusPaused;
    if (isTraversalCompleted) return t.statusCompleted;
    if (hasTraversedPages) return t.statusAnalyzedPages.replace('{count}', String(traversalState!.analyzedPages.length));
    return t.statusReady;
  };

  // Get status icon
  const getStatusIcon = () => {
    if (isMenuAnalyzing || isTraversing) return <LoadingOutlined spin />;
    if (isTraversalCompleted || hasTraversedPages) return <CheckCircleOutlined style={{ color: '#52c41a' }} />;
    return <PlayCircleOutlined />;
  };

  return (
    <Card size="small" bordered={false}>
      <Space direction="vertical" style={{ width: '100%' }} size="middle">
        {/* Header */}
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
          <Space>
            {getStatusIcon()}
            <Text strong>{getStatusText()}</Text>
          </Space>
          {(isTraversalCompleted || hasTraversedPages) && !isTraversalInProgress && (
            <Button 
              size="small" 
              icon={<ReloadOutlined />} 
              onClick={handleReset}
            >
              {t.btnRestart}
            </Button>
          )}
        </div>

        {/* Menu Analysis Progress */}
        {isMenuAnalyzing && (
          <Progress 
            percent={50} 
            size="small" 
            status="active"
            showInfo={false}
          />
        )}

        {/* Error */}
        {menuError && (
          <Alert 
            type="error" 
            message={menuError}
            showIcon
            closable
            onClose={handleReset}
          />
        )}

        {error && (
          <Alert 
            type="error" 
            message={error}
            showIcon
            closable
            onClose={handleReset}
          />
        )}

        {/* Menu Results */}
        {isMenuDone && menuState?.menu && (
          <div>
            <Text type="secondary">{t.menuItems}</Text>
            <Text strong style={{ color: '#52c41a' }}>
              {menuState.menu.items.length} {t.topLevelMenus}{t.totalItems.replace('{count}', String(countTotalMenuItems(menuState.menu.items)))}
            </Text>
          </div>
        )}

        {/* Traversal Progress */}
        {traversalState?.progress && isTraversalInProgress && (
          <>
            <Divider style={{ margin: '8px 0' }} />
            <ProgressBar progress={traversalState.progress} showDetails />
          </>
        )}

        {/* Traversal Results */}
        {hasTraversedPages && (
          <div>
            <Text type="secondary">{t.analyzedPages}</Text>
            <Text strong style={{ color: '#1890ff' }}>
              {traversalState!.analyzedPages.length}
            </Text>
          </div>
        )}

        {/* Traversal Error */}
        {traversalState?.status === 'error' && traversalState.error && (
          <Alert 
            type="error" 
            message={traversalState.error}
            showIcon
          />
        )}

        {/* Start Analysis Button - Show when idle */}
        {isIdle && (
          <Button
            type="primary"
            icon={<PlayCircleOutlined />}
            onClick={handleStartAnalysis}
            loading={isStartingAnalysis}
            block
            size="large"
          >
            {t.btnStartAnalysis}
          </Button>
        )}

        {/* Traversal Control Buttons */}
        {isTraversalInProgress && (
          <Space style={{ width: '100%' }}>
            {isTraversing && (
              <Button
                icon={<PauseCircleOutlined />}
                onClick={traversalState?.pauseTraversal}
                style={{ flex: 1 }}
              >
                {t.btnPause}
              </Button>
            )}
            
            {isTraversalPaused && (
              <Button
                type="primary"
                icon={<PlayCircleOutlined />}
                onClick={traversalState?.resumeTraversal}
                style={{ flex: 1 }}
              >
                {t.btnResume}
              </Button>
            )}
            
            <Button
              danger
              icon={<StopOutlined />}
              onClick={traversalState?.stopTraversal}
            >
              {t.btnStop}
            </Button>
          </Space>
        )}

        {/* View Code Button - Show when completed */}
        {(isTraversalCompleted || (hasTraversedPages && !isTraversalInProgress)) && (
          <Button
            type="primary"
            icon={<CodeOutlined />}
            onClick={onViewCode}
            block
          >
            {t.btnViewCode}
          </Button>
        )}

        {/* Export Button */}
        {(() => {
          const canShowExport = isTraversalCompleted || hasTraversedPages || page;
          
          if (!canShowExport) return null;
          
          // Determine which pages to export
          const pagesToExport = hasTraversedPages
            ? traversalState!.analyzedPages 
            : (page ? [page] : []);
          
          // Disable when traversing or paused
          const isDisabled = isTraversalInProgress;
          
          return (
            <>
              <Divider style={{ margin: '8px 0' }} />
              {isTraversalInProgress ? (
                <Alert
                  type="info"
                  message={t.waitExportMessage}
                  showIcon
                  style={{ marginBottom: 8 }}
                />
              ) : null}
              <ExportButton
                pages={pagesToExport}
                menu={menuState?.menu || undefined}
                sessionId={sessionId || 'default-session'}
                disabled={isDisabled || pagesToExport.length === 0}
              />
            </>
          );
        })()}
      </Space>
    </Card>
  );
};

export default AnalysisPanel;
