/**
 * PagesPanel Component - Display traversal progress and analyzed pages
 */

import React from 'react';
import { 
  Card, 
  List, 
  Empty, 
  Typography, 
  Tag, 
  Space, 
  Tooltip,
  Progress,
} from 'antd';
import { 
  CheckCircleOutlined, 
  SyncOutlined, 
  ClockCircleOutlined,
  FileTextOutlined,
  LinkOutlined,
} from '@ant-design/icons';
import type { UseTraversalReturn } from '../hooks/useTraversal';
import type { UseMenuReturn } from '../hooks/useMenu';
import type { MenuItem } from '@core/types/menu';
import { useI18n } from '../i18n';

const { Text, Paragraph } = Typography;

interface PagesPanelProps {
  traversalState: UseTraversalReturn;
  menuState: UseMenuReturn;
}

// Flatten menu items to get all navigable items
function flattenMenuItems(items: MenuItem[]): MenuItem[] {
  const result: MenuItem[] = [];
  for (const item of items) {
    if (item.path && !item.path.startsWith('#') && !item.path.startsWith('javascript:')) {
      result.push(item);
    }
    if (item.children) {
      result.push(...flattenMenuItems(item.children));
    }
  }
  return result;
}

const PagesPanel: React.FC<PagesPanelProps> = ({ traversalState, menuState }) => {
  const { t } = useI18n();
  const { status, progress, analyzedPages } = traversalState;
  const { menu } = menuState;

  const isTraversing = status === 'running';
  const isTraversalPaused = status === 'paused';
  const isTraversalInProgress = isTraversing || isTraversalPaused;
  const isTraversalCompleted = status === 'completed';

  // Get all navigable menu items
  const allMenuItems = menu ? flattenMenuItems(menu.items) : [];
  
  // Current item being traversed
  const currentItem = progress?.currentItem;
  
  // Find next item to traverse
  const completedCount = progress?.completedItems || 0;
  const nextItemIndex = completedCount;
  const nextItem = allMenuItems[nextItemIndex + 1]; // +1 because current is at completedCount index

  const getInteractionLabel = (type?: string) => {
    if (!type) return t.interactionLabelGeneric;
    switch (type) {
      case 'tab':
        return t.interactionLabelTab;
      case 'toggle':
        return t.interactionLabelToggle;
      case 'button':
        return t.interactionLabelButton;
      default:
        return t.interactionLabelGeneric;
    }
  };

  // If no menu analyzed yet
  if (!menu || allMenuItems.length === 0) {
    return (
      <Empty 
        description={t.noMenuData}
        image={Empty.PRESENTED_IMAGE_SIMPLE}
        style={{ padding: 24 }}
      />
    );
  }

  // If not started traversal yet
  if (status === 'idle' && analyzedPages.length === 0) {
    return (
      <Card size="small" bordered={false}>
        <Empty 
          description={
            <Space direction="vertical" size="small">
              <Text>{t.waitingTraversal}</Text>
              <Text type="secondary">{t.navigablePages.replace('{count}', String(allMenuItems.length))}</Text>
            </Space>
          }
          image={Empty.PRESENTED_IMAGE_SIMPLE}
        />
      </Card>
    );
  }

  return (
    <Card size="small" bordered={false} bodyStyle={{ padding: 8 }}>
      <Space direction="vertical" style={{ width: '100%' }} size="small">
        {/* Traversal Progress Summary */}
        {(isTraversalInProgress || isTraversalCompleted) && progress && (
          <div style={{ 
            padding: '8px 12px', 
            background: '#fafafa', 
            borderRadius: 4,
            marginBottom: 8,
          }}>
            <Space direction="vertical" style={{ width: '100%' }} size={4}>
              <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Text strong>
                  {isTraversing ? t.statusTraversing : isTraversalPaused ? t.statusPaused : t.statusCompleted}
                </Text>
                <Text type="secondary">
                  {progress.completedItems} / {progress.totalItems}
                </Text>
              </div>
              <Progress 
                percent={Math.round((progress.completedItems / progress.totalItems) * 100)} 
                size="small"
                status={isTraversing ? 'active' : isTraversalCompleted ? 'success' : 'normal'}
              />
            </Space>
          </div>
        )}

        {/* Current Page Being Analyzed */}
        {isTraversalInProgress && currentItem && (
          <Card 
            size="small" 
            title={
              <Space>
                <SyncOutlined spin style={{ color: '#faad14' }} />
                <Text strong>{t.currentlyAnalyzing}</Text>
              </Space>
            }
            style={{ marginBottom: 8, borderColor: '#faad14' }}
          >
            <Space direction="vertical" size={2} style={{ width: '100%' }}>
              <Text strong>{currentItem.title}</Text>
              {currentItem.path && (
                <Tooltip title={currentItem.path}>
                  <Text 
                    type="secondary" 
                    ellipsis 
                    style={{ fontSize: 12, maxWidth: '100%' }}
                  >
                    <LinkOutlined /> {currentItem.path}
                  </Text>
                </Tooltip>
              )}
            </Space>
          </Card>
        )}

        {/* Next Page to Analyze */}
        {isTraversalInProgress && nextItem && (
          <Card 
            size="small" 
            title={
              <Space>
                <ClockCircleOutlined style={{ color: '#1890ff' }} />
                <Text>{t.nextToAnalyze}</Text>
              </Space>
            }
            style={{ marginBottom: 8, borderColor: '#1890ff', opacity: 0.8 }}
          >
            <Space direction="vertical" size={2} style={{ width: '100%' }}>
              <Text>{nextItem.title}</Text>
              {nextItem.path && (
                <Tooltip title={nextItem.path}>
                  <Text 
                    type="secondary" 
                    ellipsis 
                    style={{ fontSize: 12, maxWidth: '100%' }}
                  >
                    <LinkOutlined /> {nextItem.path}
                  </Text>
                </Tooltip>
              )}
            </Space>
          </Card>
        )}

        {/* Analyzed Pages List */}
        {analyzedPages.length > 0 && (
          <>
            <div style={{ 
              display: 'flex', 
              justifyContent: 'space-between', 
              alignItems: 'center',
              padding: '4px 0',
            }}>
              <Space>
                <CheckCircleOutlined style={{ color: '#52c41a' }} />
                <Text strong>{t.analyzedPagesList}</Text>
              </Space>
              <Tag color="green">{analyzedPages.length}</Tag>
            </div>
            
            <List
              size="small"
              dataSource={analyzedPages}
              style={{ 
                maxHeight: 280, 
                overflow: 'auto',
                background: '#fafafa',
                borderRadius: 4,
              }}
              renderItem={(page, index) => (
                <List.Item 
                  key={page.id || page.url || `analyzed-${index}`}
                  style={{ 
                    padding: '8px 12px',
                    borderBottom: index < analyzedPages.length - 1 ? '1px solid #f0f0f0' : 'none',
                  }}
                >
                  <Space direction="vertical" size={2} style={{ width: '100%' }}>
                    <Space>
                      <FileTextOutlined style={{ color: '#52c41a' }} />
                      <Text strong ellipsis style={{ maxWidth: 280 }}>
                        {page.title || t.noTitle}
                      </Text>
                      {page.interactionContext && (
                        <Tag color="blue" style={{ marginInlineStart: 4 }}>
                          {getInteractionLabel(page.interactionContext.type)}: {page.interactionContext.label}
                        </Tag>
                      )}
                    </Space>
                    <Tooltip title={page.url}>
                      <Paragraph 
                        type="secondary" 
                        ellipsis 
                        style={{ fontSize: 11, marginBottom: 0, maxWidth: '100%' }}
                      >
                        {page.url}
                      </Paragraph>
                    </Tooltip>
                  </Space>
                </List.Item>
              )}
            />
          </>
        )}

        {/* Pending Pages (not yet analyzed) */}
        {isTraversalInProgress && allMenuItems.length > completedCount + 1 && (
          <>
            <div style={{ 
              display: 'flex', 
              justifyContent: 'space-between', 
              alignItems: 'center',
              padding: '4px 0',
              marginTop: 8,
            }}>
              <Space>
                <ClockCircleOutlined style={{ color: '#8c8c8c' }} />
                <Text type="secondary">{t.waitingAnalysis}</Text>
              </Space>
              <Tag>{allMenuItems.length - completedCount - 1}</Tag>
            </div>
            
            <List
              size="small"
              dataSource={allMenuItems.slice(completedCount + 2, completedCount + 7)} // Show next 5 pending
              style={{ 
                background: '#fafafa',
                borderRadius: 4,
                opacity: 0.7,
              }}
              renderItem={(item, index) => (
                <List.Item 
                  key={item.id || item.path || `pending-${index}`}
                  style={{ padding: '6px 12px' }}
                >
                  <Space>
                    <ClockCircleOutlined style={{ color: '#8c8c8c', fontSize: 12 }} />
                    <Text type="secondary" ellipsis style={{ maxWidth: 280 }}>
                      {item.title}
                    </Text>
                  </Space>
                </List.Item>
              )}
              footer={
                allMenuItems.length - completedCount - 1 > 5 ? (
                  <div style={{ textAlign: 'center', padding: '4px 0' }}>
                    <Text type="secondary" style={{ fontSize: 11 }}>
                      {t.morePages.replace('{count}', String(allMenuItems.length - completedCount - 6))}
                    </Text>
                  </div>
                ) : null
              }
            />
          </>
        )}
      </Space>
    </Card>
  );
};

export default PagesPanel;
