/**
 * ZIP Exporter - Export project as ZIP file
 */

import JSZip from 'jszip';
import type { Project, ProjectFile } from '../types/project';

/**
 * Export project to ZIP file
 */
export async function exportProjectToZip(project: Project): Promise<Blob> {
  const zip = new JSZip();
  
  // Create folder structure and add files
  for (const file of project.files) {
    const fullPath = file.path ? `${file.path}/${file.filename}` : file.filename;
    zip.file(fullPath, file.content);
  }
  
  // Generate ZIP blob
  const blob = await zip.generateAsync({
    type: 'blob',
    compression: 'DEFLATE',
    compressionOptions: { level: 6 },
  });
  
  return blob;
}

/**
 * Download ZIP file
 */
export function downloadZip(blob: Blob, filename: string): void {
  const url = URL.createObjectURL(blob);
  const link = document.createElement('a');
  link.href = url;
  link.download = filename;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
}

/**
 * Export and download project
 */
export async function exportAndDownloadProject(project: Project): Promise<void> {
  const blob = await exportProjectToZip(project);
  const filename = `${project.name.replace(/\s+/g, '-').toLowerCase()}.zip`;
  downloadZip(blob, filename);
}

/**
 * Calculate project size
 */
export function calculateProjectSize(project: Project): {
  totalSize: number;
  fileCount: number;
  byType: Record<string, { count: number; size: number }>;
} {
  const byType: Record<string, { count: number; size: number }> = {};
  let totalSize = 0;
  
  for (const file of project.files) {
    const size = new Blob([file.content]).size;
    totalSize += size;
    
    if (!byType[file.type]) {
      byType[file.type] = { count: 0, size: 0 };
    }
    byType[file.type].count++;
    byType[file.type].size += size;
  }
  
  return {
    totalSize,
    fileCount: project.files.length,
    byType,
  };
}

/**
 * Preview file content
 */
export function previewFile(file: ProjectFile): {
  content: string;
  language: string;
  truncated: boolean;
} {
  const MAX_PREVIEW_LENGTH = 10000;
  const truncated = file.content.length > MAX_PREVIEW_LENGTH;
  const content = truncated 
    ? file.content.slice(0, MAX_PREVIEW_LENGTH) + '\n\n// ...  ...'
    : file.content;
  
  // Detect language from filename
  const ext = file.filename.split('.').pop()?.toLowerCase() || '';
  const languageMap: Record<string, string> = {
    'ts': 'typescript',
    'tsx': 'typescript',
    'js': 'javascript',
    'jsx': 'javascript',
    'json': 'json',
    'css': 'css',
    'scss': 'scss',
    'less': 'less',
    'html': 'html',
    'md': 'markdown',
  };
  
  return {
    content,
    language: languageMap[ext] || 'plaintext',
    truncated,
  };
}
