/**
 * SessionList - Display and manage analysis sessions
 */

import React from 'react';
import { List, Card, Button, Space, Typography, Tag, Popconfirm, Empty } from 'antd';
import { 
  DeleteOutlined, 
  ExportOutlined, 
  FolderOutlined,
  ClockCircleOutlined,
  FileOutlined,
} from '@ant-design/icons';
import type { AnalysisSession } from '@core/types/session';

const { Text } = Typography;

interface SessionListProps {
  sessions: AnalysisSession[];
  loading?: boolean;
  onSelect?: (session: AnalysisSession) => void;
  onDelete?: (sessionId: string) => void;
  onExport?: (session: AnalysisSession) => void;
  selectedSessionId?: string;
}

/**
 * Format timestamp to readable date
 */
function formatDate(timestamp: number): string {
  const date = new Date(timestamp);
  const now = new Date();
  const diff = now.getTime() - date.getTime();
  
  // Less than 1 hour
  if (diff < 3600000) {
    const minutes = Math.floor(diff / 60000);
    return minutes <= 1 ? '' : `${minutes} `;
  }
  
  // Less than 24 hours
  if (diff < 86400000) {
    const hours = Math.floor(diff / 3600000);
    return `${hours} `;
  }
  
  // Less than 7 days
  if (diff < 604800000) {
    const days = Math.floor(diff / 86400000);
    return `${days} `;
  }
  
  // Format as date
  return date.toLocaleDateString('zh-CN', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
  });
}

/**
 * Get status tag color
 */
function getStatusColor(status: AnalysisSession['status']): string {
  switch (status) {
    case 'analyzing':
    case 'traversing':
      return 'processing';
    case 'completed':
      return 'success';
    case 'paused':
      return 'warning';
    case 'failed':
      return 'error';
    default:
      return 'default';
  }
}

/**
 * Get status text
 */
function getStatusText(status: AnalysisSession['status']): string {
  switch (status) {
    case 'analyzing':
      return '';
    case 'traversing':
      return '';
    case 'completed':
      return '';
    case 'paused':
      return '';
    case 'failed':
      return '';
    case 'exporting':
      return '';
    default:
      return '';
  }
}

const SessionList: React.FC<SessionListProps> = ({
  sessions,
  loading = false,
  onSelect,
  onDelete,
  onExport,
  selectedSessionId,
}) => {
  if (sessions.length === 0 && !loading) {
    return (
      <Card size="small" bordered={false}>
        <Empty 
          description=""
          image={Empty.PRESENTED_IMAGE_SIMPLE}
        />
      </Card>
    );
  }

  return (
    <Card size="small" bordered={false} title="">
      <List
        loading={loading}
        dataSource={sessions}
        renderItem={(session) => (
          <List.Item
            style={{
              cursor: 'pointer',
              background: selectedSessionId === session.id ? '#f0f5ff' : undefined,
              padding: '12px',
              borderRadius: 6,
              marginBottom: 8,
            }}
            onClick={() => onSelect?.(session)}
            actions={[
              onExport && (
                <Button
                  key="export"
                  type="text"
                  size="small"
                  icon={<ExportOutlined />}
                  onClick={(e) => {
                    e.stopPropagation();
                    onExport(session);
                  }}
                  disabled={session.status !== 'completed'}
                />
              ),
              onDelete && (
                <Popconfirm
                  key="delete"
                  title=""
                  description=""
                  onConfirm={(e) => {
                    e?.stopPropagation();
                    onDelete(session.id);
                  }}
                  onCancel={(e) => e?.stopPropagation()}
                  okText=""
                  cancelText=""
                >
                  <Button
                    type="text"
                    size="small"
                    danger
                    icon={<DeleteOutlined />}
                    onClick={(e) => e.stopPropagation()}
                  />
                </Popconfirm>
              ),
            ].filter(Boolean)}
          >
            <List.Item.Meta
              avatar={<FolderOutlined style={{ fontSize: 24, color: '#1890ff' }} />}
              title={
                <Space>
                  <Text strong>{session.name}</Text>
                  <Tag color={getStatusColor(session.status)}>
                    {getStatusText(session.status)}
                  </Tag>
                </Space>
              }
              description={
                <Space direction="vertical" size={0}>
                  <Space size="small">
                    <ClockCircleOutlined />
                    <Text type="secondary" style={{ fontSize: 12 }}>
                      {formatDate(session.createdAt)}
                    </Text>
                  </Space>
                  <Space size="small">
                    <FileOutlined />
                    <Text type="secondary" style={{ fontSize: 12 }}>
                      {session.analyzedPages} 
                    </Text>
                  </Space>
                </Space>
              }
            />
          </List.Item>
        )}
      />
    </Card>
  );
};

export default SessionList;
